import './App.scss';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { cloneDeep, isEqual } from 'lodash';
import { FormControl, IconButton, InputLabel, MenuItem, Select } from '@material-ui/core';
import {
  RefreshSharp as RefreshSharpIcon,
  DeleteSharp as DeleteShartIcon,
} from '@material-ui/icons';

import { ConstituencyNode, DItem, NLPLabel, VisLabel } from './@types'
import {
  SubDataTable, Chart, WordProcessor, WordProcessorRef, MapVis, Panel, LineChart, BarChart
} from './components';
import { HintData, isHintNode, Others, TaskRet, ITaskNode, OpNode } from './components/WordProcessor';
import {
  fetchData, getByCountryWeek, getByRegionDaily, getByRegionWeek, getLastWeekByCountry, getLastWeekByRegion, getReproductionData, getReproductionMap, getTrainingData
} from './data';
import { HintNode, isHintData } from './components/WordProcessor/autoHint';
import { StateMap } from './components/StateMap';
import { TrainingInstruction, Tips, Task3 } from './components/TrainingIntroduction';

export interface TableFilter {
  attr: string
  direct: 'asce' | 'desc' | 'clean'
}

export interface Layout {
  i: string,
  x: number, y: number, w: number, h: number,
  minW?: number, maxW?: number
  show: boolean
}

interface IChart {
  type: 'chart' | 'table' | 'map' | 'barchart' | 'linechart'
  datasetId: string
  data: DItem[]
  filters: TableFilter[]
}

const taskLists = ['Trainning', 'Reproduction', 'Main']

// const featureList = [
//   // Retrieve and compute value
//   'Retrieve values',
//   'Compute values',
//   // Maintain consistency
//   'Data-driven update',
//   'Reactive update',
//   'Error detection',
//   // Interactive manipulation
