export function useCompare<T>(value: T): T {
  const ref = useRef<T | undefined>();

  if (!isEqual(value, ref.current)) {
    ref.current = value;
  }

  return ref.current as T;
}

export const addByregion = (o, item) => {
  Object.keys(item).forEach(key => {
    if (key !== 'date') {
      if (!(key in o)) o[key] = 0
      o[key] += item[key]
    }
  })
  return o
}