export function TrainingInstruction() {
  const [step, setStep] = useState(0)

  const onBack = () => {
    setStep((step) => step - 1);
  }
  const onNext = () => {
    setStep((step) => step + 1);
  }

  const renderInstruction = () => {
    const curStep = instructionText[step]

    return <div className="instructionText">
      <div className="title">
        {step}.{curStep.title}
      </div>

      <div className="background">
        {/* <span className="label">Background:</span> <br /> */}
        <span dangerouslySetInnerHTML={{ __html: curStep.background }}></span>
      </div>

      {curStep.content && <div className="content">
        <em>{curStep.content}</em>
      </div>}

      {curStep.action &&
        <div className="action">
          <span className="label">Action:</span> <br />
          {Array.isArray(curStep.action)
            ? <ol>
              {curStep.action.map(p => <li dangerouslySetInnerHTML={{ __html: p }}></li>)}
            </ol>
            : <span dangerouslySetInnerHTML={{ __html: curStep.action }}></span>

          }

        </div>
      }


      {curStep.result &&
        <div className="result">
          <span className="label">Result:</span> <br /> {curStep.result}
        </div>
      }

    </div>
  }

  return <div className="instruction">
    {renderInstruction()}
    <MobileStepper
      variant="text"
      steps={instructionText.length}
      position="static"
      activeStep={step}
      className={'instructionStepper'}
      backButton={
        <Button size="small" onClick={onBack} disabled={step === 0}>
          <KeyboardArrowLeftIcon /> Back
        </Button>
      }
      nextButton={
        <Button size="small" onClick={onNext} disabled={step === instructionText.length - 1}>
          Next <KeyboardArrowRightIcon />
        </Button>
      }

    /></div>
}