import { TElement } from '@udecode/plate';
import { RangeBeforeOptions } from '@udecode/plate-common';
import { ELEMENT_OP } from '.';

export interface OpNodeData {
    opKey: string;
    opType: string;
}
export type OpNode = TElement<OpNodeData>;


export interface WithLinkOptions {
    /**
     * Allow custom config for rangeBeforeOptions.
     */
    rangeBeforeOptions?: RangeBeforeOptions;
}

export const isOpNode = (n: any): n is OpNode => typeof n === 'object' && 'type' in n && n['type'] === ELEMENT_OP