import './style.scss'
import React, { useState, useEffect, useContext } from 'react';
import { OpNodeData } from '../WordProcessor/operator'
import { StyledElementProps } from '@udecode/plate-styled-components';

import { Dropdown, Option } from '../Select';
import clsx from 'clsx';
import { ReactEditor, useSlate } from 'slate-react';
import { Editor, Transforms } from 'slate';
import { getHintNode, isDepHintNode, reactiveUpdate } from '../WordProcessor/autoHint';
import { DatatableContext } from '../WordProcessor';
import { NLPLabel } from '../../@types';

export const OpElement = (props: StyledElementProps<OpNodeData>) => {
  const { attributes, children, element: opNode, nodeProps } = props;
  const [dropDown, setDropDown] = useState<{ value: any, options: Option[] }>()
  const [alt, setAlt] = useState(false)
  const { rawData } = useContext(DatatableContext);
  const editor = useSlate()
  const path = ReactEditor.findPath(editor as any, opNode)


  useEffect(() => {
    const onMouseDown = (e: MouseEvent) => {
      setDropDown(undefined)
    }
    const onKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Alt') {
        setAlt(true)
      }
    }
    const onKeyUp = (e: KeyboardEvent) => {
      setAlt(false)
    }
    document.addEventListener('keydown', onKeyDown)
    document.addEventListener('keyup', onKeyUp)
    document.addEventListener('mousedown', onMouseDown)
    return () => {
      document.removeEventListener('keydown', onKeyDown)
      document.removeEventListener('keyup', onKeyUp)
      document.removeEventListener('mousedown', onMouseDown)
    }
  }, [])

  return (
    <a
      {...attributes}
      data-tooltip={opNode.opType}
      data-tooltip-position="top"
      // href={element.opKey}
      className={clsx('OpElement', { alt, selected: dropDown !== undefined })}
      {...nodeProps}
      onClick={e => {
        if (alt) {
          if (opNode.opType === 'computed') {
            const options = {
              value: opNode.opKey,
              options: ['total', 'average'].map((t, idx) => ({ idx, value: t }))
            }
            setDropDown(options)
          } else if (opNode.opType === 'extreme') {
            const options = {
              value: opNode.opKey,
              options: ['maximum', 'highest', 'minimum', 'lowest'].map((t, idx) => ({ idx, value: t }))
            }
            setDropDown(options)
          }
        }
      }}
    >
      {children}

      {dropDown && <div
        style={{ position: 'absolute', display: 'inline', left: 0, top: '20px' }}
        onMouseDown={e => e.stopPropagation()}
      >
        <Dropdown
          value={dropDown.value}
          options={dropDown.options}
          onChange={newOption => {
            console.group('Select From Dropdown ===>')
            console.debug('newOption:', newOption)


            if (opNode.opType === 'computed' || opNode.opType === 'extreme') {
              console.group('Change a computed opNode')
              console.debug('newData', 'path', path)
              Transforms.setNodes(editor, { opKey: newOption.value } as any, { at: path })
              Transforms.insertText(editor, newOption.value, { at: [...path, 0], voids: true })

              // should then trigger some reactive update
              console.group('Triger reactive update')
              for (const [node, nodePath] of Editor.nodes(editor, { at: [path[0]], match: isDepHintNode, voids: true })) {
                // re-evaluate
                console.log('hintNode re-evaluate', node)
                if (!node.deps) {
                  console.warn('Dep node has no deps', node)
                  continue
                }
                const datasetKey = node.deps.tokens.filter(t => t.hintPath)
                  .map(t => getHintNode(t, editor as any))
                  .find(h => h?.datasetId)?.datasetId
                const dataTable = rawData[datasetKey]
                console.log(datasetKey)
                // should update the tokens
                // string match
                const tokens = (node.deps.tokens as NLPLabel[]).map(token => {
                  if (token.text === opNode.children[0].text) {
                    return { ...token, text: newOption.value, lemma: newOption.value }
                  }
                  return token
                }) as NLPLabel[]
                const result = reactiveUpdate(tokens, node.deps.constituency, node as any, nodePath, dataTable, rawData, editor as any)
                // returnNode = result.returnNode
                // others = result.others

              }
              console.groupEnd()


              setDropDown(undefined)
              console.groupEnd()
            }
          }}
        >
        </Dropdown>
      </div>}
    </a>
  );
};