/**
 * Get previous index from 0 to max.
 * If index is 0, get to max.
 */
export const getPreviousIndex = (i: number, max: number) =>
  i <= 0 ? max : i - 1;

/**
 * Get next index from 0 to max.
 * If index is max, get to 0.
 */
export const getNextIndex = (i: number, max: number) => (i >= max ? 0 : i + 1);

export function maxItem<T extends DItem>(arr: T[], key: string) {
  return [...arr].reduce((o, d) => +o.data[key] > +d.data[key] ? o : d)
}
export function minItem<T extends DItem>(arr: T[], key: string) {
  return [...arr].reduce((o, d) => +o.data[key] < +d.data[key] ? o : d)
}

/**
 * Filter Op
 */
export function lessThanItem<T extends DItem>(arr: T[], target: number | string, key: string) {
  return [...arr].filter(item => item.data[key] < +target)
}
export function largerThanItem<T extends DItem>(arr: T[], target: number | string, key: string) {
  return [...arr].filter(item => item.data[key] > +target)
}
export function equalToItem<T extends DItem>(arr: T[], target: string | number, key: string) {
  console.log('target', target, 'key', key)
  return [...arr].filter(item => item.data[key].toString() === target.toString())
}
export function allItem<T extends DItem>(arr: T[], target: any, key: string) {
  return [...arr]
}
/**
 * The previous one
 */
export function lastUnit<T extends DItem>(arr: T[], unit: number | string = 1, attr: string) {
  if (typeof unit === 'string') unit = +unit
  return [...arr].slice(1 * unit, 2 * unit)
}

/**
 * Compute
 */
export function sum(values: number[]): number;
export function sum<T extends DItem>(arr: T[], key: string): number;
export function sum(items: any[], key?: string) {
  if (key) {
    return [...items].reduce((o, d) => o + +d.data[key], 0)
  }
  return [...items].reduce((o, d) => o + +d, 0)
}

export function avg(values: number[]): number;
export function avg<T extends DItem>(arr: T[], key: string): number;
export function avg(items: any[], key?: string) {
  if (key) return sum(items, key) / items.length
  return +(sum(items) / items.length).toFixed(2)
}

export function abs(values: number[]): number {
  return Math.abs(values[0])
}


export async function NLPParser(search: string, sentence?: SplitSentence) {
  if (!sentence?.sentence) return { nlpTokens: [], constituency: null }

  // parse the sentence, get nlp tags
  // if the searchIsEmpty, add a placeholder
  const rawtext = search === ''
    ? `${sentence?.sentence}TTOKEN`
    : sentence?.sentence

  // const apiUrl = 'http://ddw.chenzhutian.org/nlp'
    // const apiUrl = 'http://localhost:9901/nlp'
  // const apiUrl = 'https://creativitybot.ucsd.edu/nlp'
  const apiUrl ='http://ec2-18-163-101-211.ap-east-1.compute.amazonaws.com/nlp'
  const nlpTokens: { deps: SpacyRet[], constituency: ConstituencyNode } = await fetch(apiUrl, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({ rawtext })
  }).then(d => d.json())
  return { nlpTokens: nlpTokens.deps, constituency: nlpTokens.constituency }
}

export function addHintData(editor: SPEditor, nlpTokens: SpacyRet[], sentence?: SplitSentence) {
  if (!sentence?.sentence) return []
  return sentence?.tokens.map((p, token_idx) => {
    const { token, path } = p

    // get the bounding box of the token
    let hintNode: HintNode | undefined
    let hintPath: Path | undefined
    if (path.path.length >= 3) {
      const [parentNode, parentPath] = Editor.parent(editor, path.path)
      // this should be a hint node
      if (!isHintNode(parentNode)) {
        console.warn('path', path, token)
        console.warn('The parentNode is not a hint node!', parentNode)
      } else {
        hintNode = parentNode
        hintPath = parentPath
      }
    }

    const nlpToken = nlpTokens.find(t => t.token_idx === token_idx)
    return nlpToken ? {
      ...nlpToken,
      // hintNode,
      hintPath,
    } : undefined
  }).filter(l => l !== undefined) as NLPLabel[]
}

export function addVisData(editor: SPEditor, nlpLabels: SpacyRet[], sentence?: SplitSentence) {
  if (!sentence?.sentence) return []

  return sentence?.tokens.map((p, token_idx) => {
    const { token, path } = p

    // get the bounding box of the token
    let bbox: DOMRect
    if (path.path.length < 3) {
      const range = ReactEditor.toDOMRange(editor as any, {
        anchor: { path: path.path, offset: path.offset - token.length },
        focus: { path: path.path, offset: path.offset },
      })
      bbox = range?.getBoundingClientRect()
    } else {
      const [parentNode] = Editor.parent(editor, path.path)
      // this should be a hint node
      console.log('parentNode', parentNode)
      const dom = ReactEditor.toDOMNode(editor as any, parentNode)
      bbox = dom.getBoundingClientRect()
    }

    const nlpLabel = nlpLabels.find(t => t.token_idx === token_idx)
    return nlpLabel ? {
      ...nlpLabel,
      x: bbox.x,
      y: bbox.y,
    } : undefined
  }).filter(l => l !== undefined) as VisLabel[]
}

export function findByIdx(idx: number, labels: NLPLabel[]) {
  return labels.find(l => l.token_idx === idx)
}

export function headOf(idx: number, labels: NLPLabel[]): NLPLabel | undefined;
export function headOf(label: NLPLabel, labels: NLPLabel[]): NLPLabel | undefined;
export function headOf(query: number | NLPLabel, labels: NLPLabel[]) {
  return (typeof query === 'number')
    ? labels.find(l => l.token_idx === findByIdx(query, labels)?.head_idx)
    : labels.find(l => l.token_idx === query.head_idx)
}

export function headsOf(label: NLPLabel, labels: NLPLabel[]) {
  const heads: NLPLabel[] = []
  let head = headOf(label, labels)
  let count = 0
  while (head) {
    heads.push(head)
    if (head.dep === 'ROOT') break

    head = headOf(head, labels)
    ++count
    if (count > 100) {
      console.error('Try too many times to get the head', head)
      break
    }
  }
  console.log('heads', heads)
  return heads
}

export function childrenOf(token: NLPLabel, tokens: NLPLabel[]) {
  return tokens.filter(l => l.head_idx === token.token_idx)
}
export function attrChildrenOf(token: NLPLabel, tokens: NLPLabel[], editor: SPEditor) {
  return childrenOf(token, tokens).filter(l => {
    const [hintNode] = Editor.node(editor, l.hintPath || [])
    return isHintNode(hintNode) && hintNode.dataType === 'attr'
  })
}

export function getHintNode(token: NLPLabel, editor: SPEditor) {
  if (!token.hintPath) return
  const [hintNode] = Editor.node(editor, token.hintPath || [])
  if (!isHintNode(hintNode)) {
    console.warn('Path ', token, ' is not a HintNode')
    return
  }
  return hintNode
}

/**
 * @todo maybe need to remove duplicate attrs
 * @param token 
 * @param tokens 
 * @returns 
 */
export function flatAttrChildrenOf(token: NLPLabel, tokens: NLPLabel[], editor: SPEditor): Array<{ token: NLPLabel, attr: NLPLabel }> {
  const directChild = attrChildrenOf(token, tokens, editor)
    .flatMap(attr => {
      const conjAttrTokens = attr.conjuncts
        .map(ct => findByIdx(ct.token_idx, tokens))
        .filter(ct => ct !== undefined) as NLPLabel[]
      return [
        { token: attr, attr },
        ...conjAttrTokens.map(cattr => ({ token: attr, attr: cattr }))
      ]
    })

  // prep + attr
  const children = childrenOf(token, tokens)
    .flatMap(child => {
      // need to get all children with prep + attr
      const prepChildren = childrenOf(child, tokens).filter(cc => cc.dep === 'prep')
      return prepChildren.flatMap(prepChild => attrChildrenOf(prepChild, tokens, editor))
        .map(prepAttr => ({ attr: prepAttr, token: child }))

    })

  return [...directChild, ...children]
}

export function valueChildrenOf(label: NLPLabel, tokens: NLPLabel[], editor: SPEditor) {
  return childrenOf(label, tokens)
    .filter(l => {
      const [hintNode] = Editor.node(editor, l.hintPath || [])
      return isHintNode(hintNode) && hintNode.dataType === 'value'
    })
}

export function getCurrentSentence(selection: BaseRange, editor: BaseEditor): [number, SplitSentence[]] {
  const cursor = Range.start(selection);

  // get the current sentence
  const [parentNode, parentPos] = Editor.parent(editor!, selection.anchor.path)

  // 0. iterate all text node, and concate them to get the text
  const range = Editor.range(editor, parentPos)
  const frags: Array<{ path: Path, text: string }> = []
  for (const [node, path] of Editor.nodes(editor, {
    at: range,
    match: Text.isText,
    voids: true,
  })) {
    frags.push({ path, text: node.text })
  }

  // 1. split the sentences
  const sentences = splitSentences(frags)

  // 2. find the current sentence
  const sentenceIdx = sentences.findIndex(s => {
    const pId = cursor.path.join('_')
    const point = s.paths[pId]
    return point && cursor.offset <= point.offset
  })
  // console.log('getCurrentSentence:', sentences[sentenceIdx], 'from ', sentences)
  return [sentenceIdx, sentences]
}

export function getDataPhrases(sentence: SplitSentence, editor: Editor) {
  const dataPhrases = sentence.tokens
    .reduce((o, p, token_idx) => {
      const { path } = p
      if (path.path.length >= 3) {
        const [parentNode] = Editor.parent(editor, path.path)
        // this should be a hint node
        if (isHintNode(parentNode)) {
          if (!o.includes(parentNode))
            o.push(parentNode)
        } else if (isOpNode(parentNode)) {
          if (!o.includes(parentNode))
            o.push(parentNode)
        } else {
          console.warn('The parentNode is not a hint node!', parentNode)
        }
      }
      return o
    }, [] as Array<HintNode | OpNode>)

  return dataPhrases
}

