export function flatDataTableToCell(dataItems: DItem[]): Cell[] {
  return dataItems.flatMap(({ data, rowIdx, id }) => Object.entries(data).map(([attr, v], colIdx) => ({
    rowId: id, attr, colIdx, rowIdx, value: v.toString()
  })))
}

