
import { insertNodes } from '@udecode/plate-common';
import {
    getPlatePluginType,
    SPEditor,
} from '@udecode/plate-core';
import { Transforms } from 'slate';
import { HintNode, HintData, ELEMENT_HINT } from './types';

export function insertHint(editor: SPEditor, data: HintData) {
    const pluginKey = ELEMENT_HINT
    let children
    if (data.nodeType === 'indep') {
        const tokens = data.value.split(' ').flatMap(s => s.split('_'))
        const targetIdx = tokens.findIndex((t) => t.toLowerCase().includes(data.query.toLowerCase()))
        const regEx = new RegExp(data.query, "ig");
        children = [{ text: tokens[targetIdx].replace(regEx, data.query) }]
    } else {
        children = [{ text: data.value }]
    }

    // it must be an evlauted value
    console.log('update hintNode??')
    const hintNode: HintNode = {
        ...data,
        type: getPlatePluginType(editor, pluginKey),
        children
    };
    insertNodes<HintNode>(editor, hintNode);
    Transforms.move(editor);
}