import { ConstituencyNode } from "../../../../@types";
import { HintNode } from "../types";
import { AddupOp, CompareOp, ComputeOp, ExtremumOp, FilterOp } from "./OpNode";

export enum TASK_TYPE {
    retrieve_value = 'retrieve_value',
    find_extremum = 'find_extremum',
    filter = 'filter',
    derived_value = 'derived_value',
    distribution = 'distribution',
    outlier = 'outlier',
    correlation = 'correlation',
    negation = 'negation',
    trend = 'trend',
    comparison = 'comparison',
    add_up = 'add_up',
    range = 'range'
    // RESULT = 'RESULT'
}

interface BaseTaskNode {
    node: ConstituencyNode
    lv: number
    task: TASK_TYPE | ''
    op?: (...args: any[]) => any
    hintNode?: HintNode
    subTask?: ITaskNode[]
}
const isTaskNode = (n: any): n is BaseTaskNode => typeof n === 'object' && 'task' in n && 'node' in n && 'lv' in n && typeof n['lv'] === 'number'

/** 
 * Extremem Task
 */
export interface ExtremeTask extends BaseTaskNode { task: TASK_TYPE.find_extremum, op: ExtremumOp, name: 'min' | 'max' }

/**
 * Filter Task
 */
export interface FilterTask extends BaseTaskNode { task: TASK_TYPE.filter, op: FilterOp, oData?: string }

/**
 * Compute task
 */
export interface ComputedTask extends BaseTaskNode { task: TASK_TYPE.derived_value, op: ComputeOp, hintNode: HintNode }

/**
 * Retrieve Task
 */
export interface RetrieveTask extends BaseTaskNode { task: TASK_TYPE.retrieve_value }

/**
 * Comparson Task
 */
export interface ComparisonTask extends BaseTaskNode { task: TASK_TYPE.comparison, op: CompareOp, notDoneYet?: boolean }

/**
 * Range
 */
export interface RangeTask extends BaseTaskNode { task: TASK_TYPE.range }

/**
 * AddUp task
 */
export interface AddupTask extends BaseTaskNode { task: TASK_TYPE.add_up, op: AddupOp, oData?: string }

/**
 * RESULT task
 */
// export interface RESULTTask extends BaseTaskNode { task: TASK_TYPE.RESULT }

export type ITaskNode = ExtremeTask | FilterTask | ComputedTask | RetrieveTask | ComparisonTask | AddupTask | RangeTask

const is = {
    taskNode: isTaskNode,
    Retrieve: (n: any): n is RetrieveTask => isTaskNode(n) && n.task === TASK_TYPE.retrieve_value,
    Computed: (n: any): n is ComputedTask => isTaskNode(n) && n.task === TASK_TYPE.derived_value,
    Filter: (n: any): n is FilterTask => isTaskNode(n) && n.task === TASK_TYPE.filter,
    Extreme: (n: any): n is ExtremeTask => isTaskNode(n) && n.task === TASK_TYPE.find_extremum,
    Comparison: (n: any): n is ComparisonTask => isTaskNode(n) && n.task === TASK_TYPE.comparison,
    Addup: (n: any): n is AddupTask => isTaskNode(n) && n.task === TASK_TYPE.add_up,
    Range: (n: any): n is RangeTask => isTaskNode(n) && n.task === TASK_TYPE.range,
}

// eslint-disable-next-line import/no-anonymous-default-export
export default {
    is
}