import {
    PlatePluginKey,
    SPEditor,
    TElement,
} from '@udecode/plate-core';
import { Range, Node } from 'slate';

import { ConstituencyNode, DItem, NLPLabel, VisLabel } from '../../../@types';
import { OpNode } from '../operator';
import { ITaskNode } from './taskEngine/TaskNode';

export const ELEMENT_HINT = 'HINT';

interface BaseHintData {
    value: string
    // dataType: 'attr' | 'value' | 'computed' | 'placeholder',
    // nodeType: 'indep' | 'dep'
    attr: string
    colIdx: number
    rowIdx: number
    selectedIdx?: number
    rowId: string
}

export interface PlaceholderHintData extends BaseHintData {
    nodeType: 'placeholder'
    dataType: 'value' | 'computed' | 'attr'
    phType: 'VALUE' | 'COUNT_ITEMS' | 'DIFF_ATTR' | 'RATIO'
    valueFormat?: (...args: any[]) => any
    deps: { tokens: NLPLabel[], constituency: ConstituencyNode } | null
}

export interface InDepHintData extends BaseHintData {
    nodeType: 'indep'
    dataType: 'attr' | 'value'
    datasetId: string
    query: string
}
export interface DepHintdata extends BaseHintData {
    nodeType: 'dep'
    dataType: 'value' | 'computed'
    // datasetId: string
    valueFormat?: (...args: any[]) => any
    deps: { tokens: NLPLabel[], constituency: ConstituencyNode }
}

export type HintData = PlaceholderHintData | InDepHintData | DepHintdata
export const isHintData = (n: any): n is HintData => typeof n === 'object' && 'nodeType' in n && 'dataType' in n && 'attr' in n && 'value' in n

export type Others = {
    type: 'sum' | 'min' | 'max' | 'compare'
    items: DItem[],
    attr?: string
}

export type TaskRetItem = { type: 'item', data: DItem[], others?: Others }
export type TaskRetValue = { type: 'value', data: DepHintdata[], others?: Others }
export type TaskRet = TaskRetItem | TaskRetValue

export type HintNode = TElement<HintData>;
export const isHintNode = (n: any): n is HintNode => typeof n === 'object' && 'type' in n && n['type'] === ELEMENT_HINT

export const isDepHintNode = (n: Node): n is HintNode => isHintNode(n) &&
    (n.nodeType === 'dep' || n.nodeType === 'placeholder')
export const isInDepHintNode = (n: Node): n is HintNode => isHintNode(n) &&
    (n.nodeType === 'indep')

export interface HintPluginOptions extends PlatePluginKey {
    // dataTable: DItem[];
    rawData: Record<string, DItem[]>

    onTab?: (labels: VisLabel[], tokens: NLPLabel[], constituency: ConstituencyNode | null) => void
    onSelectHint?: (candidates?: HintData, taskTree?: ITaskNode, others?: Others) => void
    onFocusCurSentence?: (dataPhrases?: Array<HintData | OpNode>, taskTree?: ITaskNode, res?: TaskRet[]) => void
    onChangeDataPhrases?: (dataPhrases: Array<Array<HintData | OpNode>>) => void
    onOthers?: (others: Others, valueFormat?: (...args: any[]) => any) => void
}

export interface GetHintSelectProps {
    /**
     * Range from the hints trigger to the cursor
     */
    at: Range | null;

    /**
     * List of hintable items
     */
    options: Array<HintData>;
    valueIndex: number;

    /**
     * Callback called when clicking on a hint option
     */
    onClickHint?: (editor: SPEditor, option: HintData) => void;
}