import './style.scss'
import React, { useEffect, useMemo, useRef, useState } from 'react'
import * as d3 from 'd3'
import { chunk, cloneDeep, uniq } from 'lodash'
import { FormControl, InputLabel, MenuItem, Select } from '@material-ui/core';
import { ToggleButton, ToggleButtonGroup } from '@material-ui/lab';

import { DItem } from '../../@types'
import { HintData } from '../WordProcessor/autoHint'
import { useCompare } from '../../App'
import { isOpNode, OpNode } from '../WordProcessor/operator'

interface Props {
  data: DItem[]
  focused?: Array<HintData | OpNode>
  allDataPhrases?: Array<Array<HintData | OpNode>>
  onUpdateBrush?: (from: string, to: string) => void
}

// const focusHeight = 110
const height = 400
const width = 700
const margin = { top: 20, right: 20, bottom: 30, left: 40 }

const xAxis = (g, x) => g
  .attr("transform", `translate(0,${height - margin.bottom})`)
  .call(d3.axisBottom(x).tickSizeOuter(0)
    .tickValues(
      x.domain()
        .filter((d, i) => !(i % 7))
    )
    .tickFormat((d, i) => {
      return `W${i}` // d.toString().slice(5)
    })
  )
  .call(g => g.selectAll(".domain").remove())

const yAxis = (g, y) => g
  .attr("transform", `translate(${margin.left},0)`)
  .call(d3.axisLeft(y).ticks(null, "s"))
  .call(g => g.selectAll(".domain").remove())


export const Chart = React.memo((props: Props) => {
  let { focused = [], allDataPhrases = [] } = props
  focused = useCompare(focused)
  allDataPhrases = useCompare(allDataPhrases)
  const data = useCompare(props.data ?? [])
  const dataField = 'new_cases'

  const [groupBy, setGroupBy] = useState<'daily' | 'weekly' | null>(null)
  const [hlRegionOptions, setHLRegionOptions] = useState<string[] | null>(null)
  const [hlRegion, setHLRegion] = useState<string | null>(null)
  const svgRef = useRef<SVGSVGElement | null>(null)

  const { series, items } = useMemo(() => {
    // get the new_cases data
    let items = data.map(item => ({
      ...item,
      data: Object.keys(item.data).reduce((o, k) => {
        if (k === 'date') o[k] = item.data[k]
        else o[k] = item.data[k][dataField]
        return o
      }, {})
    }))
    // either change to weekly
    const byWeek = groupBy === null
      ? focused.some(d => isOpNode(d) && d.opKey === 'week')
      : groupBy
    if (byWeek === true || byWeek === 'weekly') {
      const chunkItems = chunk(items, 7)
      items = chunkItems.map((chunk, idx) => {
        const data = chunk.reduce((o, item) => {
          if (Object.keys(o).length === 0) {
            o = { ...item.data }
          } else {
            Object.keys(item.data)
              .forEach(k => {
                if (k !== 'date') o[k] += item.data[k]
              })
          }
          return o
        }, {} as Record<string, any>)

        return { id: data['date'], rowIdx: idx, virtualAttr: [], data } as DItem
      })
    }

    // or highlight the specific region
    const highlightedRegions = hlRegion === null
      ? focused.filter(d => d.attr === 'region' && d.dataType === 'value').map(d => d.value)
      : hlRegion === 'all' ? [] : [hlRegion]
    const regions = [
      ...highlightedRegions,
      ...Object.keys(items[0]?.data || {}).slice(1).filter(r => !highlightedRegions.includes(r))
    ]

    const series = d3.stack()
      .keys(regions)(items.map(d => d.data))
      .map(d => {
        d['highlighted'] = highlightedRegions.length > 0 ? highlightedRegions.includes(d.key) : true
        return d
      }) || []

    return { series, items }
  }, [data, focused, groupBy, hlRegion])

  useEffect(() => {
    const flatDataPhrases = allDataPhrases.flatMap(d => d)
    const byWeek = flatDataPhrases.some(d => isOpNode(d) && d.opKey === 'week')
    setGroupBy(byWeek ? 'weekly' : null)

    const highlightedRegions = uniq(flatDataPhrases.filter(d => d.attr === 'region' && d.dataType === 'value').map(d => d.value))
    setHLRegionOptions(highlightedRegions.length ? highlightedRegions : null)
    setHLRegion(highlightedRegions.length ? highlightedRegions[highlightedRegions.length - 1] : null)
  }, [allDataPhrases])

  const x = d3.scaleBand()
    .domain([...items].reverse().map(d => d.data['date'] as string))
    .range([margin.left, width - margin.right])
    .padding(0.1)

  const y = d3.scaleLinear()
    .domain([0, d3.max(series, d => d3.max(d, d => d[1])) as number])
    .rangeRound([height - margin.bottom, margin.top])

  const brushed = ({ selection }) => {
    if (selection) {
      const domain = x.domain();
      const paddingOuter = x(domain[0]) || 0;
      const eachBand = x.step();

      const [minX, maxX] = selection.map(v => {
        const index = Math.floor((v - paddingOuter) / eachBand)
        return domain[Math.max(0, Math.min(index, domain.length - 1))];
      })
      // console.log('minX', minX, 'maxX', maxX)
      props.onUpdateBrush?.(minX, maxX)
    }
  }

  function brushended(this: any, { sourceEvent, selection }) {
    if (!sourceEvent) return;
  }

  const brush = d3.brushX<any>()
    .extent([[margin.left, margin.top], [width - margin.right, height - margin.bottom]])
    .on("brush", brushed)
    .on("end", brushended);

  const gb = d3.select(svgRef.current)
    .select<SVGGElement>('g#brush')
    .call(brush)

  useEffect(() => {
    d3.select(svgRef.current)
      .select('g#gx')
      .call(xAxis, x);

    d3.select(svgRef.current)
      .select('g#gy')
      .call(yAxis, y)

  }, [svgRef, x, y])

  // brush
  useEffect(() => {
    console.log('Chart focused', focused)
    const dates = focused.filter(f => f.attr === 'date')
    if (dates.length === 0) return

    // create selection
    if (dates.length === 1) {
      dates.push(cloneDeep(dates[0]))
    }

    // calculate the extent
    const defaultSelection = [x(dates[0].value), x(dates[1].value)];

    // move the brush
    gb.call(brush.move, defaultSelection);
  }, [brush.move, focused, gb, x])

  const color = d3.scaleOrdinal<string>()
    .domain(Object.keys(data[0]?.data || {}).slice(1))
    .range(d3.schemeSpectral[6]) // dirty hack
    .unknown("#ccc")

  return <div className="chartWrapper">
    <div className="controls">
      {groupBy &&
        <ToggleButtonGroup
          size="small"
          exclusive
          aria-label="data-basis"
          value={groupBy}
          onChange={(_, v) => { setGroupBy(v) }}
        >
          <ToggleButton value="daily" aria-label="left aligned">
            {"Daily"}
          </ToggleButton>
          <ToggleButton value="weekly" aria-label="centered">
            {"Weekly"}
          </ToggleButton>
        </ToggleButtonGroup>
      }

      {hlRegionOptions &&
        <FormControl style={{ minWidth: '80px' }} size="small" variant='outlined'>
          <InputLabel id="select-outlined-label">Region</InputLabel>
          <Select
            labelId="select-outlined-label"
            id="simple-select-outlined"
            value={hlRegion}
            onChange={(e) => {
              console.log('highlight region', e.target.value)
              const value: string = e.target.value as string
              setHLRegion(value)
            }}
            displayEmpty
            label="Region"
          >
            <MenuItem value={'all'}>All</MenuItem>
            {hlRegionOptions.map(r => <MenuItem value={r}>{r}</MenuItem>)}
          </Select>
        </FormControl>
      }
    </div>

    <svg id="vis" ref={svgRef} viewBox={`0 0 ${width} ${height}`} className={'chart'}>
      {series.map(serie => {
        return <g key={serie.key} fill={color(serie.key)} opacity={serie['highlighted'] ? 1 : 0.4}>
          {serie.map(d => {
            return <rect
              key={`${d.data['date']}_${serie.key}`}
              className={'bar'}
              x={`${x(d.data['date'].toString())}`}
              y={`${y(d[1])}`}
              height={`${y(d[0]) - y(d[1])}`}
              width={`${x.bandwidth()}`}
            >
            </rect>
          })}
        </g>
      })}
      <g id="gx"></g>
      <g id="gy"></g>
      <g id="brush"></g>
    </svg>
  </div>
})
Chart.displayName = 'Chart'
Chart.whyDidYouRender = true

