import './style.scss'
import React, { useEffect, useMemo, useRef, useState } from 'react'
import * as d3 from 'd3'
import { cloneDeep, isEqual, uniq } from 'lodash'

import { DItem } from '../../@types'
import { HintData, OpNode } from '../WordProcessor'
import { useCompare } from '../../App'
import { FormControl, InputLabel, MenuItem, Select } from '@material-ui/core'

const margin = { top: 20, right: 20, bottom: 30, left: 40 }
const height = 600
const width = 900

const xAxis = (g, x) => g
  .attr("transform", `translate(0,${height - margin.bottom})`)
  .call(d3.axisBottom(x).ticks(width / 80).tickSizeOuter(0))

const yAxis = (g, y) => g
  .attr("transform", `translate(${margin.left},0)`)
  .call(d3.axisLeft(y))
  .call(g => g.select(".domain").remove())
  .call(g => g.select(".tick:last-of-type text").clone()
    .attr("x", 3)
    .attr("text-anchor", "start")
    .attr("font-weight", "bold")
    // .text(data.y))
  )

interface Props {
  data: DItem[]
  focused?: Array<HintData | OpNode>
  allDataPhrases?: Array<Array<HintData | OpNode>>
  onUpdateBrush?: (from: Date, to: Date) => void
}

export const LineChart = React.memo((props: Props) => {
  const { data: rawData = [], focused = [], allDataPhrases = [], onUpdateBrush } = props
  // focused = useCompare(focused)
  // allDataPhrases = useCompare(allDataPhrases)
  const svgRef = useRef<SVGSVGElement | null>(null)
  const [dotPos, setDotPos] = useState<{ x: number, y: number, text: string, data: { name: string, values: number[] } } | null>(null)

  const [hlStateOptions, setHLStateOptions] = useState<string[] | null>(null)
  const [hlState, setHLState] = useState<string | null>(null)

  const { series, dates } = useMemo(() => {

    const highlightedStates = hlState === null
      ? focused.filter(d => d.attr !== 'date' && d.dataType === 'attr').map(d => d.value)
      : hlState === 'all' ? [] : [hlState]

    const series = Object.keys(rawData[0].data).filter(name => name !== 'date').map((name) => {
      return { name, isFocus: highlightedStates.length === 0 || hlState === name, values: rawData.map(d => d.data[name]) }
    }) as { name: string, isFocus: boolean, values: number[] }[]
    const dates = rawData.map(d => new Date(d.id)) as Date[]

    return { series, dates }

  }, [focused, hlState, rawData])

  useEffect(() => {
    const flatDataPhrases = allDataPhrases.flatMap(d => d).filter(d => 'datasetId' in d && d['datasetId'] === 'reProductionData')
    const highlightedRegions = uniq(flatDataPhrases.filter(d => d.attr !== 'date' && d.dataType === 'attr').map(d => d.value))
    setHLStateOptions(highlightedRegions.length ? highlightedRegions : null)
    setHLState(highlightedRegions.length ? highlightedRegions[highlightedRegions.length - 1] : null)
  }, [allDataPhrases])

  const x = d3.scaleUtc()
    .domain(d3.extent(dates) as [Date, Date])
    .range([margin.left, width - margin.right])

  const y = d3.scaleSqrt()
    .domain([0, d3.max(series, d => d3.max(d.values)) as number]).nice()
    .range([height - margin.bottom, margin.top])

  const line = d3.line<number>()
    .defined(d => !isNaN(+d))
    .x((d, i) => x(dates[i]))
    .y(d => y(+d))

  const brush = useMemo(() => d3.brushX<any>()
    .extent([[margin.left, margin.top], [width - margin.right, height - margin.bottom]])
    .on("brush", ({ selection }) => {
      if (selection) {
        const [minX, maxX] = selection.map(x.invert, x)

        onUpdateBrush?.(minX, maxX)
      }
    }), [onUpdateBrush, x])

  const gb = useMemo(() => d3.select(svgRef.current)
    .select<SVGGElement>('g#brush')
    .call(brush), [brush])

  useEffect(() => {
    d3.select(svgRef.current)
      .select('g#gx')
      .call(xAxis, x);

    d3.select(svgRef.current)
      .select('g#gy')
      .call(yAxis, y)

  }, [svgRef, x, y])

  // brush
  useEffect(() => {
    console.log('Chart focused', focused)
    const dates = focused.filter(f => f.attr === 'date')
    if (dates.length === 0) return

    // create selection
    if (dates.length === 1) {
      dates.push(cloneDeep(dates[0]))
    }

    // calculate the extent
    const defaultSelection = [x(new Date(dates[0].value)), x(new Date(dates[1].value))];

    // move the brush
    gb.call(brush.move, defaultSelection);
  }, [brush.move, focused, gb, x])


  const color = d3.scaleOrdinal<string>()
    .domain(series.map(d => d.name))
    // .range(d3.schemeSpectral[data.series.length]) // dirty hack
    .range(d3.schemeTableau10)
    .unknown("#ccc")

  return <div className='chartWrapper'>

    <div className="controls">
      {hlStateOptions &&
        <FormControl style={{ minWidth: '80px' }} size="small" variant='outlined'>
          <InputLabel id="select-outlined-label">State</InputLabel>
          <Select
            labelId="select-outlined-label"
            id="simple-select-outlined"
            value={hlState}
            onChange={(e) => {
              console.log('highlight region', e.target.value)
              const value: string = e.target.value as string
              setHLState(value)
            }}
            displayEmpty
            label="Region"
          >
            <MenuItem value={'all'}>All</MenuItem>
            {hlStateOptions.map(r => <MenuItem value={r}>{r}</MenuItem>)}
          </Select>
        </FormControl>
      }
    </div>


    <svg ref={svgRef} viewBox={`0 0 ${width} ${height}`} overflow="visable" className="chart"
      // onMouseLeave={() => {
      //   setDotPos(null)
      // }}
      // onMouseMove={(event) => {
      //   event.preventDefault();
      //   if (!svgRef.current || hlStateOptions?.length) return

      //   const svg = svgRef.current.ownerSVGElement || svgRef.current;
      //   let point = svg.createSVGPoint();
      //   point.x = event.clientX
      //   point.y = event.clientY;
      //   point = point.matrixTransform(svgRef.current.getScreenCTM()!.inverse());
      //   const px = point.x
      //   const py = point.y

      //   const xm = x.invert(px);
      //   const ym = y.invert(py);
      //   const i = d3.bisectCenter(dates, xm);
      //   const s = d3.least(series, d => Math.abs(d.values[i] - ym))!;

      //   const newPos = { x: x(dates[i]), y: y(s.values[i]), text: s.name, data: s }
      //   if (!isEqual(newPos, dotPos)) {
      //     setDotPos(newPos)
      //   }
      // }}
    >
      <g className="lines">
        {series.map(serie => {
          return <path
            key={serie.name}
            stroke={
              (hlStateOptions && hlStateOptions?.length > 0)
                ? serie.isFocus ? color(serie.name) : '#ddd'
                : (!dotPos || dotPos.data.name === serie.name) ? color(serie.name) : '#ddd'
            }
            style={{
              mixBlendMode: dotPos ? 'initial' : 'multiply'
            }}
            d={line(serie.values)!}

          ></path>
        })}
      </g>
      {dotPos && <g id="dot" transform={`translate(${dotPos.x}, ${dotPos.y})`}>
        <circle r="2.5"></circle>
        <text y="-8">{dotPos.text}</text>
      </g>}

      <g id="gx"></g>
      <g id="gy"></g>
      <g id="brush"></g>
    </svg>
  </div>
})
LineChart.displayName = 'LineChart'
