import Papa from 'papaparse'
import { chunk } from 'lodash'

import reproductionData from './state_cases.json'
import reproductionMap from './more_state_cases.json'

import { DItem } from '../@types'
import { dates as rDates } from '../components/LineChart/dates'

const reNameRegion = {
  AMRO: 'Americas',
  AFRO: 'Africa',
  SEARO: 'South_East_Asia',
  EURO: 'Europe',
  EMRO: 'Eastern_Mediterranean',
  WPRO: 'Western_Pacific'
}

type RawData = {
  date: string
  country: string
  region: string
  new_cases: number
  total_cases: number
  new_deaths: number
  total_deaths: number
}

export const fetchData = async () => {
  let rawData = localStorage.getItem('rawData')
  if (!rawData) {
    rawData = await fetch(`${process.env.PUBLIC_URL}/byDailyCountry.csv`).then(res => res.text())
    localStorage.setItem('rawData', rawData!)
  }
  const results = Papa.parse(rawData, { header: true, dynamicTyping: true });

  return results.data as RawData[]
}

export function getByRegionDaily(rawData: RawData[]): DItem[] {
  return Object.entries(rawData.reduce((o, item) => {
    if (!(item.date in o)) {
      o[item.date] = []
    }
    o[item.date].push(item)
    return o
  }, {} as Record<string, RawData[]>)).map(([date, items]) => {
    // then group by region
    const byRegion = items.reduce((o, item) => {
      if (!(item.region in o)) {
        o[item.region] = { new_cases: 0, new_deaths: 0, total_cases: 0, total_deaths: 0 }
      }
      o[item.region] = {
        new_cases: o[item.region].new_cases + item.new_cases,
        new_deaths: o[item.region].new_deaths + item.new_deaths,
        total_cases: o[item.region].total_cases + item.total_cases,
        total_deaths: o[item.region].total_deaths + item.total_deaths
      }
      return o
    }, {})
    const day = { date, ...byRegion }

    return day
  })
    .map(({ date, ...item }, rowIdx) => ({
      id: date, rowIdx, virtualAttr: [],
      data: {
        date: date.slice(0, 10).replaceAll('-', '/'),
        ...Object.keys(item).reduce((o, region) => {
          // hardcode to new_cases
          o[reNameRegion[region]] = item[region]
          return o
        }, {}),
      }
    })).reverse()
}

export function getByCountryWeek(rawData: RawData[]): DItem[] {
  const byDate = Object.entries(rawData.reduce((o, item) => {
    if (!(item.date in o)) {
      o[item.date] = []
    }
    o[item.date].push(item)
    return o
  }, {} as Record<string, RawData[]>))

  const byWeek = chunk(byDate.sort((a, b) => {
    const d1 = new Date(a[0])
    const d2 = new Date(b[0])
    return d2.getTime() - d1.getTime()
  }).map(d => d[1]), 7)
    .map(countryIn7Days => {

      return countryIn7Days.reduce((o, countryIn1Day) => {

        const item = countryIn1Day.reduce((o, country) => {
          if (!('date' in o)) o['date'] = country.date.replaceAll('-', '/')
          if (!(country.country in o)) {
            o[country.country] = {
              region: reNameRegion[country.region] || country.region,
              new_cases: country.new_cases,
              new_deaths: country.new_deaths,
              total_cases: country.total_cases,
              total_deaths: country.total_deaths
            }
          }
          return o
        }, {})

        if (Object.keys(o).length === 0) {
          o = item
        } else {
          o = Object.keys(o).reduce((oo, country) => {
            if (country === 'date') {
              oo[country] = o[country]
              return oo
            }
            oo[country] = {
              region: o[country].region,
              new_cases: o[country].new_cases + item[country].new_cases,
              new_deaths: o[country].new_deaths + item[country].new_deaths,
              total_cases: o[country].total_cases + item[country].total_cases,
              total_deaths: o[country].total_deaths + item[country].total_deaths
            }
            return oo
          }, {})
        }
        return o
      }, {})
    }).map((data, rowIdx) => ({ id: data['date'], rowIdx, virtualAttr: [], data }))

  return byWeek
}

export function getLastWeekByCountry(byWeek: DItem[]): DItem[] {
  const week = byWeek[0]
  if (!week) return []
  return Object.entries(week.data).filter(([country]) => country !== 'date')
    .map(([country, data], rowIdx) => {
      const { date, region, ...restData } = data
      return {
        id: country, rowIdx, virtualAttr: [],
        data: { country, region: reNameRegion[region] || region, ...restData }
      }
    })
}

export function getByRegionWeek(byDay: DItem[]): DItem[] {
  const chunkItems = chunk(byDay, 7)
  return chunkItems.map((regionIn7Days, rowIdx) => {

    const data = regionIn7Days.map(d => d.data).reduce((o, item) => {
      // region
      if (Object.keys(o).length === 0) o = item
      else {
        const { date, ...regions } = item
        o = Object.keys(o).reduce((oo, key) => {
          if (key === 'date') oo[key] = o[key]
          else {
            oo[key] = {
              new_cases: o[key].new_cases + regions[key].new_cases,
              new_deaths: o[key].new_deaths + regions[key].new_deaths,
              total_cases: o[key].total_cases + regions[key].total_cases,
              total_deaths: o[key].total_deaths + regions[key].total_deaths,
            }
          }
          return oo
        }, {})
      }

      return o
    }, {})

    return { id: data['date'], rowIdx, virtualAttr: [], data }
  })
}

export function getLastWeekByRegion(byRegionWeek: DItem[]): DItem[] {
  const week = byRegionWeek[0]
  if (!week) return []
  return Object.entries(week.data)
    .filter(([key]) => key !== 'date')
    .map(([region, data], rowIdx) => {
      return {
        id: region, rowIdx, virtualAttr: [], data: { region, ...data }
      }
    })
}

export function getTrainingData(): DItem[] {

  return [
    { day: 'Monday', money: 150 },
    { day: 'Tuesday', money: 20 },
    { day: 'Wednesday', money: 70 },
    { day: 'Thursday', money: 180 },
    { day: 'Friday', money: 200 },
    { day: 'Saturday', money: 240 },
    { day: 'Sunday', money: 340 },
  ].map((d, rowIdx) => ({
    id: d.day, rowIdx, virtualAttr: [], data: { ...d }
  }))
}


export function getReproductionData(): DItem[] {

  return rDates.map((date, rowIdx) => {

    date = date.slice(0, 10).replaceAll('-', '/')

    return {
      id: date, rowIdx, virtualAttr: [], data: {
        date, ...reproductionData.reduce((o, state) => {
          o[state.name] = state.values[rowIdx + 6] ?? 0
          return o
        }, {})
      }

    }
  }).reverse()
}



export function getReproductionMap(): DItem[] {

  return reproductionMap.map((d, rowIdx) => {
    const values = d.values as number[]

    const cases = Math.round(values.filter(v => v !== null).reduce((o, v: number) => o + v, 0))
    return {
      id: d.name, rowIdx, virtualAttr: [], data: { state: d.name, cases }
    }
  })

}