import React, { useEffect, useMemo, useState } from 'react';
import { IconButton, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import {
  CloseSharp as DeleteIcon,
  CheckSharp as CheckIcon,
  ArrowUpwardSharp as ArrowUpwardSharpIcon,
  ArrowDownwardSharp as ArrowDownwardSharpIcon,
  RemoveSharp as RemoveSharpIcon
} from '@material-ui/icons';
import FlipMove from 'react-flip-move';
import clsx from 'clsx';

import './style.scss'

import { DItem } from '../../@types';
import { green, red, blueGrey } from '@material-ui/core/colors';
import { HintData, isHintData } from '../WordProcessor/autoHint';
import { TableFilter } from '../../App';
import { OpNode } from '../WordProcessor';

interface Props {
  data: DItem[]
  idx: number
  datasetId: string
  highlighted?: HintData | undefined
  focused?: Array<HintData | OpNode>
  filters?: TableFilter[]
  onEditCell?: (id: string, field: string, value: string, chartIdx: number) => void
  onAcceptRow?: (rowId: string, chartIdx: number) => void
  onRejectRow?: (rowId: string, chartIdx: number) => void
  onAcceptCol?: (attr: string, chartIdx: number) => void
  onRejectCol?: (attr: string, chartIdx: number) => void
  onRemoveFilter?: (attr: string, chartIdx: number) => void
  onToggleSorter?: (filter: TableFilter, chartIdx: number) => void
}

interface Row {
  id: string
  virtual?: boolean
  virtualAttr: string[]
  data: {
    [key: string]: {
      value: any,
      isHighlightedH?: boolean
      isHighlightedV?: boolean
      isFocused: boolean
    }
  }
}

interface Head {
  field: string
  headerName: string
  sortable: boolean
  editable: boolean
  isHighlightedV?: boolean
  isFocused?: boolean
  virtual: boolean
}

export const SubDataTable = React.memo((props: Props) => {
  let { highlighted, data = [], filters, onEditCell, focused = [], datasetId } = props
  const [editingCell, setEditingCell] = useState<{ id: string, attr: string, cellId: string, value: string } | null>(null)

  highlighted = (highlighted && 'datasetId' in highlighted && highlighted['datasetId'] === datasetId) ? highlighted : undefined
  const focusedHintNode = useMemo(() => {
    const hintNOdes = focused?.filter(isHintData).filter(d => 'datasetId' in d && d['datasetId'] === datasetId)
    console.log('hintNOdes', hintNOdes, focused)
    return hintNOdes
  }, [datasetId, focused])
  // const onEditCellChangeCommitted = React.useCallback(
  //   ({ id, field, props }) => {
  //     onEditCell?.(id, field, props.value)
  //   },
  //   [onEditCell],
  // );

  const heads: Head[] = Object.keys(data[0]?.data || {}).map(h => {
    // .filter(hintData => hintData.colIdx !== 0)
    const isFocused = focusedHintNode
      ? focusedHintNode?.some(hintData => hintData.attr === h && hintData.dataType === 'attr')
      && !focusedHintNode.some(hintData => (hintData.attr === h && hintData.dataType === 'value') ||
        (hintData.colIdx === 0 && hintData.dataType === 'value')
      )
      : false

    const isHighlightedV = (highlighted?.attr === h
      && !(highlighted?.dataType === 'value' && highlighted?.colIdx === 0))
      || (highlighted && isFocused)

    return {
      field: h,
      headerName: h,
      sortable: true,
      editable: true,
      isHighlightedV,
      isFocused,
      virtual: data[0].virtualAttr.includes(h)
    }
  })

  heads.push({
    field: 'ctrl',
    headerName: '',
    sortable: false,
    editable: true,
    virtual: false,
  })

  const rows: Row[] = data
    .map(({ data, rowIdx, id, virtual, virtualAttr }) => {
      // const isFocusedRow = focused?.some(hintData => hintData.dataType === 'value' && hintData.colIdx === 0 && rowIdx === hintData.rowIdx)

      data = Object.keys(data).reduce((o, h) => {

        const isFocused = focusedHintNode?.length !== 0
          ? focusedHintNode?.every(hintData => (hintData.dataType === 'attr' && hintData.attr === h)
            || (hintData.dataType === 'value' && hintData.colIdx === 0 && rowIdx === hintData.rowIdx)
            || (hintData.dataType === 'value' && hintData.colIdx !== 0 && rowIdx === hintData.rowIdx && hintData.attr === h)
          )
          : false

        // if column is highlighted
        const isHighlightedV = (highlighted?.attr === h && highlighted.colIdx !== 0) || heads.find(head => head.headerName === h)?.isHighlightedV // || (highlighted && isFocused)
        const isHighlightedH = (highlighted?.dataType === 'value' && highlighted.rowIdx === rowIdx) // || (highlighted && isFocused)

        o[h] = {
          value: data[h],
          isFocused, //: focused?.some(fr => fr.rowIdx === rowIdx), // && heads[dataPhrase.colIdx].isFocused)
          isHighlightedV,
          isHighlightedH,
        }
        return o
      }, {})
      return { data, id, virtual, virtualAttr }
    })


  useEffect(() => {

    const cleanEditing = (e: MouseEvent) => {
      if (!editingCell) return
      const cellElm = document.querySelector<HTMLTableDataCellElement>(`#subTable_${props.idx} #${editingCell?.cellId}`);
      if (cellElm !== null && e.target !== cellElm) {
        // should commit change
        console.log('old value', editingCell.value)
        onEditCell?.(editingCell.id, editingCell.attr, cellElm.innerText, props.idx)
        setEditingCell(null)
      }
    }
    const onEnter = (e: KeyboardEvent) => {
      if (!editingCell) return
      if (e.key === 'Enter') {
        e.preventDefault()
        const cellElm = document.querySelector<HTMLTableDataCellElement>(`#subTable_${props.idx} #${editingCell?.cellId}`);
        if (cellElm !== null) {
          // should commit change
          onEditCell?.(editingCell.id, editingCell.attr, cellElm.innerText, props.idx)
          setEditingCell(null)
        }
      }
    }
    document.addEventListener('mousedown', cleanEditing)
    document.addEventListener('keydown', onEnter)
    return () => {
      document.removeEventListener('mousedown', cleanEditing)
      document.removeEventListener('keydown', onEnter)
    }
  }, [editingCell, onEditCell, props.idx])

  return <TableContainer>
    <Table id={`subTable_${props.idx}`} className={'table'} size="small" aria-label="simple table">
      <TableHead>
        <TableRow>
          {heads.map(head => {
            const filter = filters?.find(d => d.attr === head.field)
            const { virtual } = head
            return <TableCell
              key={`top_${head.field}`}
              title={head.headerName}
              className={clsx('head', 'cell', 'upper')}>
              {filter &&
                <>
                  <IconButton
                    style={{ color: blueGrey[500] }}
                    size="small"
                    aria-label="asce-filter"
                    onClick={() => props.onToggleSorter?.(filter, props.idx)}
                  >
                    {filter.direct === 'asce'
                      ? <ArrowUpwardSharpIcon />
                      : filter.direct === 'clean'
                        ? <RemoveSharpIcon />
                        : <ArrowDownwardSharpIcon />}
                  </IconButton>
                </>
              }
              {
                virtual && <>
                  <IconButton style={{ color: green[500] }} aria-label="accept-col"
                    onClick={() => {
                      props.onAcceptCol?.(head.field, props.idx)
                    }}>
                    <CheckIcon />
                  </IconButton>
                  <IconButton style={{ color: red[500] }} aria-label="reject-col"
                    onClick={() => {
                      props.onRejectCol?.(head.field, props.idx)
                    }}
                  >
                    <DeleteIcon />
                  </IconButton>
                </>
              }
              {!(filter || virtual) && <div style={{ padding: '20px' }}></div>}
            </TableCell>
          })}
        </TableRow>
        <TableRow>
          {heads.map(h => <TableCell
            key={`head_${h.headerName}`}
            title={h.headerName}
            className={clsx('head', 'cell', {
              'highlighted-v': h?.isHighlightedV,
              focused: h.isFocused,
              virtual: h.virtual,
            })}>
            {h.headerName}
          </TableCell>)}
        </TableRow>
      </TableHead>
      <TableBody style={{ position: 'relative' }}>
        <FlipMove typeName={null} staggerDurationBy={15} staggerDelayBy={20} duration={700} easing={'ease'}>
          {rows.map((row) => (
            <TableRow key={row.id} className={clsx('row', {
              virtual: row.virtual
            })}>
              {Object.entries(row.data).map(([h, value]) =>
                <TableCell
                  key={`cell_${row.id}_${h}`}
                  id={`cell_${row.id}_${h}`}
                  className={clsx('cell', {
                    'highlighted-v': value.isHighlightedV,
                    'highlighted-h': value.isHighlightedH,
                    focused: value.isFocused,
                    virtual: row.virtualAttr.includes(h),
                    editing: editingCell?.id === row.id && editingCell?.attr === h
                  })}
                  title={value.value}
                  contentEditable={editingCell?.id === row.id && editingCell?.attr === h}
                  // onInput={e => {
                  //   console.log('onInput', (e.target as HTMLTableDataCellElement).innerText)
                  //   console.log(e)
                  // }}
                  onKeyDown={e => {
                    if (e.key === 'Enter') {
                      e.preventDefault()
                    }
                  }}
                  onDoubleClick={e => {
                    // console.log('onDbclick a cell', e)
                    setEditingCell({ id: row.id, attr: h, cellId: `cell_${row.id}_${h}`, value: value.value })
                  }}
                >
                  {value.value}
                </TableCell>)}

              {row.virtual &&
                <TableCell className={'cell ctrl'} style={{ width: '100px' }}>
                  <IconButton style={{ color: green[500] }} aria-label="accept-row"
                    onClick={() => {
                      props.onAcceptRow?.(row.id, props.idx)
                    }}>
                    <CheckIcon />
                  </IconButton>
                  <IconButton style={{ color: red[500] }} aria-label="reject-row"
                    onClick={() => {
                      props.onRejectRow?.(row.id, props.idx)
                    }}
                  >
                    <DeleteIcon />
                  </IconButton>
                </TableCell>
              }
            </TableRow>
          ))}
        </FlipMove>
      </TableBody>
    </Table>
  </TableContainer>

})
SubDataTable.displayName = 'SubDataTable'