import { Path, Point } from 'slate';

import * as Match from './Match'

const newline_placeholder = " @~@ ";
const newline_placeholder_t = newline_placeholder.trim();

const whiteSpaceCheck = new RegExp("\\S", "");
// const addNewLineBoundaries = new RegExp("\\n+|[-#=_+*]{4,}", "g");
// const splitIntoWords = new RegExp("\\S+|\\n", "g");

interface Options {
    newline_boundaries: boolean
    preserve_whitespace: boolean
    abbreviations: null
}

export interface SplitSentence {
    paths: Record<string, Point>
    sentence: string
    tokens: Array<{ token: string, path: Point }>
    raw: Array<{ token: string, path: Point }>
}

export function splitSentences(frags: Array<{ path: Path, text: string }>, user_options: Partial<Options> = {}): SplitSentence[] {
    const text = frags.reduce((o, f) => o + f.text, "")

    // whitespace-only string has no sentences
    if (!text.length || !whiteSpaceCheck.test(text)) {
        return [];
    }

    const options: Options = {
        ...{
            newline_boundaries: false,
            preserve_whitespace: true,
            abbreviations: null
        },
        ...user_options
    }

    Match.setAbbreviations(options.abbreviations);

    // Split the text into words
    // let words: string[] | null = []
    // let tokens: Array<{ token: string, path: Path }>
    // Split the text into words
    // <br> tags are the odd man out, as whitespace is allowed inside the tag
    // tokens = text.split(/(<br\s*\/?>|\S+|\n+)/);
    const tokens = frags.reduce((o: Array<{ token: string, path: Point }>, frag, i) => {
        let offset = 0
        let n = frag.text.split(/(<br\s*\/?>|\S+|\n+)/)
            .map(t => ({ token: t, path: { path: frag.path, offset: offset += t.length } }))

        // console.log('token', n)
        // every other token is a word
        // words = words!.concat(n.filter((_, ii) => ii % 2).map(d => d.token))
        // remove the empty string, except those with only one token

        if (n.length !== 1 && i !== 0) n = n.filter(t => t.token.length)
        return o.concat(n)
    }, [])
    // const words = tokens.filter((d, ii) => ii % 2).map(d => d.token)
    const words = tokens.filter((d, ii) => /\S/.test(d.token)).map(d => d.token)

    // If given text is only whitespace (or nothing of \S+)
    if (words === null || !words.length) {
        return [];
    }

    let wordCount = 0;
    let sentences: string[][] = [];
    let current: string[] = [];
    for (let i = 0, L = words.length; i < L; i++) {
        wordCount++;
        const word = words[i]
        // Add the word to current sentence
        current.push(word);
        // Sub-sentences, reset counter
        if (~word.indexOf(",")) {
            wordCount = 0;
        }

        if (Match.isBoundaryChar(word) ||
            word.endsWith("?!") ||
            word === newline_placeholder_t) {

            if (options.newline_boundaries && word === newline_placeholder_t) {
                current.pop();
            }
            sentences.push(current);

            wordCount = 0;
            current = [];

            continue;
        }

        // if (word.endsWith("\"") || word.endsWith("")) {
        //     word = word.slice(0, -1);
        // }

        // A dot might indicate the end sentences
        // Exception: The next sentence starts with a word (non abbreviation)
        //            that has a capital letter.
        if (word.endsWith(".")) {
            // Check if there is a next word
            // This probably needs to be improved with machine learning
            if (i + 1 < L) {
                // Single character abbr.
                if (word.length === 2 && isNaN(+word.charAt(0))) {
                    continue;
                }

                // Common abbr. that often do not end sentences
                if (Match.isCommonAbbreviation(word)) {
                    continue;
                }

                // Next word starts with capital word, but current sentence is
                // quite short
                if (Match.isSentenceStarter(words[i + 1])) {
                    if (Match.isTimeAbbreviation(word, words[i + 1])) {
                        continue;
                    }

                    // Dealing with names at the start of sentences
                    if (Match.isNameAbbreviation(wordCount, words.slice(i, 6))) {
                        continue;
                    }

                    if (Match.isNumber(words[i + 1])) {
                        if (Match.isCustomAbbreviation(word)) {
                            continue;
                        }
                    }
                } else {
                    // Skip ellipsis
                    if (word.endsWith("..")) {
                        continue;
                    }

                    //// Skip abbreviations
                    // Short words + dot or a dot after each letter
                    if (Match.isDottedAbbreviation(word)) {
                        continue;
                    }

                    if (Match.isNameAbbreviation(wordCount, words.slice(i, 5))) {
                        continue;
                    }
                }
            }

            sentences.push(current);
            current = [];
            wordCount = 0;

            continue;
        }

        // Check if the word has a dot in it
        let index: number = word.indexOf(".")
        if (index > -1) {
            if (Match.isNumber(word, index)) {
                continue;
            }

            // Custom dotted abbreviations (like K.L.M or I.C.T)
            if (Match.isDottedAbbreviation(word)) {
                continue;
            }

            // Skip urls / emails and the like
            if (Match.isURL(word) || Match.isPhoneNr(word)) {
                continue;
            }
        }

        let temp = Match.isConcatenated(word)
        if (temp) {
            current.pop();
            current.push(temp[0]);
            sentences.push(current);

            current = [];
            wordCount = 0;
            current.push(temp[1]);
        }
    }

    if (current.length) {
        sentences.push(current);
    }

    // Clear "empty" sentences
    sentences = sentences.filter(s => s.length > 0)

    const result = sentences.slice(1).reduce((out, sentence) => {
        const lastSentence = out[out.length - 1];

        // Single words, could be "enumeration lists"
        if (lastSentence.length === 1 && /^.{1,2}[.]$/.test(lastSentence[0])) {
            // Check if there is a next sentence
            // It should not be another list item
            if (!/[.]/.test(sentence[0])) {
                out.pop()
                out.push(lastSentence.concat(sentence));
                return out;
            }
        }

        out.push(sentence);
        return out;
    }, [sentences[0]]);

    // join tokens back together
    return result.filter(s => s.length > 0 && s[0].length > 0).map((sentence, ii) => {

        // tokens looks like so: [leading-space token, non-space token, space
        // token, non-space token, space token... ]. In other words, the first
        // item is the leading space (or the empty string), and the rest of
        // the tokens are [non-space, space] token pairs.
        // let tokenCount = sentence.length * 2;

        if (ii === 0) {
            // tokenCount += 1;
            tokens.shift()
        }

        const tmpTokens: Array<{ token: string; path: Point }> = []
        // skip the first empty space, if
        if (!/\S/.test(tokens[0].token) && tokens[0].token !== sentence[0]) {
            tokens.shift()
        }
        for (let j = 0, len = sentence.length; j < len;) {
            const firstToken = tokens.shift()
            if (firstToken === undefined) {
                console.warn('Something wrong! the firstToken is undefined', firstToken)
                break
            }
            tmpTokens.push(firstToken)
            if (firstToken.token === sentence[j]) {
                ++j
            }
        }
        // add the last empty space, if
        if (tokens.length === 1 && /\s+/.test(tokens[0].token) && tmpTokens.length !== 0) {
            tmpTokens.push(tokens[0])
            tokens.shift()
        }

        const paths = tmpTokens.reduce((o, t) => {
            const pId = t.path.path.join('_')
            o[pId] = t.path
            return o
        }, {})

        return {
            paths,
            sentence: tmpTokens.map(t => t.token).join(''),
            raw: tmpTokens,
            tokens: tmpTokens.filter(p => p.token.match(/\S+/))
        }
    });
}

