import "./style.scss"
import * as React from 'react';
import { useEffect, useRef } from 'react';
import { PortalBody } from '@udecode/plate-styled-components';
import {
  useEventEditorId,
  useStoreEditorRef
} from '@udecode/plate-core';
import { ReactEditor } from 'slate-react';
import clsx from 'clsx';

import { HintSelectProps } from './types';

export const HintSelect = (props: HintSelectProps) => {
  const {
    at,
    options,
    valueIndex,
    onClickHint,
  } = props

  const ref: any = useRef();
  const editor = useStoreEditorRef(useEventEditorId('focus'));

  useEffect(() => {
    if (at && options.length > 0) {
      const el = ref.current;
      const domRange = ReactEditor.toDOMRange(editor!, at);
      const rect = domRange.getBoundingClientRect();
      if (el) {
        el.style.top = `${rect.top + window.pageYOffset + 24}px`;
        el.style.left = `${rect.left + window.pageXOffset}px`;
      }
    }
  }, [options.length, editor, at]);

  if (!at || !options.length) {
    return null;
  }

  const renderPreview = () => {
    const option = options[valueIndex]

    let imgSrc = ''
    if (option && 'datasetId' in option) {
      switch (option['datasetId']) {
        case 'trainingData':
          imgSrc = 'pTTable.png'
          break
        case 'trainingData2':
          imgSrc = 'pTChart.png'
          break
        case 'reProductionMap':
          imgSrc = 'pMap.png'
          break
        case 'reProductionData':
          imgSrc = 'pLine.png'
          break
        case 'lastWeekByRegion':
          imgSrc = 'pMTable.png'
          break
        case 'lastWeekByRegion2':
          imgSrc = 'pMChart.png'
          break
        case 'lastWeekByCountry':
          imgSrc = 'pMMap.png'
          break
      }
    }

    return imgSrc ? <div className={'hintPreview'}>
      <img src={`${process.env.PUBLIC_URL}/${imgSrc}`}>
      </img>
    </div> : ''
  }

  return (
    <PortalBody>
      <div ref={ref}
        className={clsx('slate-HintSelect')}>
        {options.map((option, i) => {
          const tokens = option.value.split(' ').flatMap(s => s.split('_'))
          const targetIdx = option.nodeType !== 'indep'
            ? 0
            : tokens.findIndex((t: string) => t.toLowerCase().includes(option.query.toLowerCase()))

          return <div
            key={`${option.rowIdx}_${option.value}_${('datasetId' in option) ? option['datasetId'] : ''}`}
            className={clsx('hint-item', { selected: i === valueIndex })}
          >
            <span className={'value'}>
              {tokens.slice(0, targetIdx).join(' ') + ' '}
              {<span className="query">{tokens[targetIdx]}</span>}
              {' ' + tokens.slice(targetIdx + 1).join(' ')}
            </span>
            <span className={'more'}>{option.attr}</span>
            {/* {renderLabel(option)} */}
          </div>
        })}
        {renderPreview()}
      </div>
    </PortalBody>
  );
}