import React, { useEffect, useMemo, useRef, useState } from 'react'
import * as d3 from 'd3'

import { cloneDeep, isEqual } from 'lodash'
import { HintData, OpNode } from '../WordProcessor'
import { DItem } from '../../@types'
import clsx from 'clsx'
import { useCompare } from '../../App'

const margin = { top: 20, right: 20, bottom: 30, left: 40 }
const height = 300
const width = 500

const xAxis = (g, x) => g
  .attr("transform", `translate(0,${height - margin.bottom})`)
  .call(d3.axisBottom(x).tickSizeOuter(0))

const yAxis = (g, y) => g
  .attr("transform", `translate(${margin.left},0)`)
  .call(d3.axisLeft(y))
  .call(g => g.select(".domain").remove())
  .call(g => g.append("text")
    .attr("x", -margin.left)
    .attr("y", 10)
    .attr("fill", "currentColor")
    .attr("text-anchor", "start")
    .text('Amount'))

interface Props {
  data: DItem[]
  focused?: Array<HintData | OpNode>
  allDataPhrases?: Array<Array<HintData | OpNode>>
  onUpdateBrush?: (fromItem: DItem, toItem: DItem) => void
}

export const BarChart = React.memo((props: Props) => {
  let { data, focused = [], allDataPhrases = [], onUpdateBrush } = props
  focused = useCompare(focused)
  data = useCompare(data.sort((a, b) => a.rowIdx - b.rowIdx))

  const [dotPos, setDotPos] = useState<{ day: string } | null>(null)

  const svgRef = useRef<SVGSVGElement | null>(null)

  const x = useMemo(() => d3.scaleBand()
    .domain(data.map(d => d.id))
    .range([margin.left, width - margin.right])
    .padding(0.1), [data])

  const y = d3.scaleLinear()
    .domain([0, d3.max(data, d => d.data.money)!]).nice()
    .range([height - margin.bottom, margin.top])

  const brush = useMemo(() => d3.brushX<any>()
    .extent([[margin.left, margin.top], [width - margin.right, height - margin.bottom]])
    .on("brush", ({ selection }) => {
      if (selection) {
        const domain = x.domain();
        const paddingOuter = x(domain[0]) || 0;
        const eachBand = x.step();

        const [minX, maxX] = selection.map(v => {
          const index = Math.floor((v - paddingOuter) / eachBand)
          return domain[Math.max(0, Math.min(index, domain.length - 1))];
        })

        const minItem = data.find(d => d.id === minX)
        const maxItem = data.find(d => d.id === maxX)
        if (minItem && maxItem) {
          onUpdateBrush?.(minItem, maxItem)
        }
      }
    }), [data, onUpdateBrush, x])

  const gb = useMemo(() => d3.select(svgRef.current)
    .select<SVGGElement>('g#brush')
    .call(brush), [brush])

  useEffect(() => {

    d3.select(svgRef.current)
      .select('g#gx')
      .call(xAxis, x);

    d3.select(svgRef.current)
      .select('g#gy')
      .call(yAxis, y)

  }, [data, svgRef, x, y])

  useEffect(() => {
    const days = focused.filter(f => f.attr === 'day')
    if (days.length === 0) return

    if (days.length === 1) {
      days.push(cloneDeep(days[0]))
    }
    console.log('focused', focused)

    const defaultSelection = [x(days[0].rowId), x(days[1].rowId)! + x.bandwidth()];

    // move the brush
    gb.call(brush.move, defaultSelection);
  }, [brush.move, focused, gb, x])


  const renderTooltip = () => {
    const item = data.find(d => d.id === dotPos?.day)!
    const px = x(item.id)! + x.bandwidth() / 2
    const py = y(item.data.money) - 5

    return <g id="dot" transform={`translate(${px}, ${py})`}>
      <circle r="2.5"></circle>
      <text fill="#313131" y="-8" x="-40">Amount: {item.data.money}</text>
    </g>
  }

  return <div className='chartWrapper'>

    <svg ref={svgRef} viewBox={`0 0 ${width} ${height}`} overflow="visable" fill='steelblue'
      // onMouseMove={(event) => {
      //   event.preventDefault();
      //   if (!svgRef.current) return
      //   const svg = svgRef.current.ownerSVGElement || svgRef.current;
      //   let point = svg.createSVGPoint();
      //   point.x = event.clientX
      //   point.y = event.clientY;
      //   point = point.matrixTransform(svgRef.current.getScreenCTM()!.inverse());
      //   const px = point.x

      //   const domain = x.domain();
      //   const paddingOuter = x(domain[0]) || 0;
      //   const eachBand = x.step();

      //   const index = Math.floor((px - paddingOuter) / eachBand)
      //   const data = domain[Math.max(0, Math.min(index, domain.length - 1))];

      //   const newPos = { day: data }
      //   if (!isEqual(newPos, dotPos)) {
      //     setDotPos(newPos)
      //   }
      // }}

      // onMouseLeave={() => {
      //   setDotPos(null)
      // }}
    >
      {data.map((item, i) => {
        const fRows = focused.filter(f => f.dataType === 'value')
        return <rect
          key={item.id}
          className={clsx({ unFocused: fRows.length > 0 && fRows.every(f => f.rowIdx !== item.rowIdx) })}
          x={x(item.id)}
          y={y(item.data.money)}
          height={y(0) - y(item.data.money)}
          width={x.bandwidth()}
        >
        </rect>
      })}

      {dotPos && renderTooltip()}
      <g id="gx"></g>
      <g id="gy"></g>
      <g id="brush"></g>
    </svg>
  </div>
})
BarChart.displayName = 'BarChart'