import "./style.scss"
import { IconButton, Paper } from '@material-ui/core'
import React, { forwardRef } from 'react'
import {
  BookmarkSharp as BookmarkSharpIcon,
  TableChartSharp as TableChartSharpIcon
} from '@material-ui/icons';

type Props = {
  title: string
  insights?: string[]
  minH?: number
  onSave?: () => void
  onShowData?: () => void
} & React.HTMLAttributes<HTMLDivElement>

export const Panel = React.memo(forwardRef<HTMLDivElement, Props>((props, ref) => {
  const { title, className, insights = [], minH = 0, onSave, onShowData, children, ...restOfProps } = props

  return <Paper className={`panel ${className}`} ref={ref} {...restOfProps}>
    <div className="header">
      <h4 className={'title'}>{title}</h4>
      <div className={'toolbars'}>
        {/* {onShowData && <IconButton aria-label="chart" color="primary"
          onClick={onShowData}
        >
          <TableChartSharpIcon />
        </IconButton>} */}
        {onSave && <IconButton aria-label="chart" color="primary"
          onClick={onSave}
        >
          <BookmarkSharpIcon />
        </IconButton>}
      </div>
    </div>
    <div style={{ minHeight: `${minH}px` }}
      className="wrapper">
      {children}

      {(insights.length > 0) &&
        <div className="insight-list">
          <h4 className="title">Insights found in this chart/table:</h4>
          <ol>
            {insights.map(insight => <li key={insight} >{insight}</li>)}
          </ol>
        </div>
      }

    </div>
  </Paper>
}))
Panel.displayName = 'Panel'