import React, { useMemo } from 'react'
import * as d3 from 'd3'
import * as topojson from 'topojson-client'
import us from './states-albers-10m.json'
import { DItem } from '../../@types'

const width = 975
const height = 610

const path = d3.geoPath()

interface Props {
  data: DItem[]
}

export function StateMap(props: Props) {
  const { data } = props

  const color = useMemo(() => d3.scaleSequentialSymlog()
    .domain(d3.extent(data.map(d => d.data.cases)) as [number, number])
    .interpolator(d3.interpolateYlGnBu)
    .unknown("#ccc"), [data])

  return <svg display='block' viewBox={`0 0 ${width} ${height}`} className={'map'}>

    <g fill="#ccc">
      {topojson.feature(us, us.objects.states).features.map(d => {
        const stateName = d.properties.name
        return <path key={d.properties.name}
          fill={color(data.find(state => state.id === stateName)?.data.cases)}
          d={path(d)!}
          // opacity={data.get(d.properties.name)?.focuse ? 1 : 0.2}
          className="state"
        >
          {/* <title>{`${d.properties.name} ${data.has(d.properties.name) ? data.get(d.properties.name)?.data[attr] : "N/A"}`}
                    </title> */}
        </path>
      })}
    </g>

    <path fill="none"
      stroke="white"
      strokeLinejoin="round"
      pointerEvents="none"
      d={path(topojson.mesh(us, us.objects.states, (a, b) => a !== b))!}></path>

    <path fill='none' stroke='black' strokeLinejoin="round" ></path>

  </svg>

}