import './style.scss'
import React, { useEffect, useMemo, useState } from 'react'
import * as d3 from 'd3'
import { ExtendedGeometryCollection, GeoGeometryObjects } from 'd3'
import { FormControl, InputLabel, MenuItem, Select } from '@material-ui/core';
import { ToggleButton, ToggleButtonGroup } from '@material-ui/lab';
import * as topojson from 'topojson-client'
import { uniq, isEqual } from 'lodash';

import world from './countries-50m.json'
import { DItem } from '../../@types'
import { useCompare } from '../../App'
import { HintData, OpNode } from '../WordProcessor'

const width = 975

const rename = new Map([
  ["Antigua and Barbuda", "Antigua and Barb."],
  ["Bolivia (Plurinational State of)", "Bolivia"],
  ["Bosnia and Herzegovina", "Bosnia and Herz."],
  ["Brunei Darussalam", "Brunei"],
  ["Central African Republic", "Central African Rep."],
  ["Cook Islands", "Cook Is."],
  ["Democratic People's Republic of Korea", "North Korea"],
  ["Democratic Republic of the Congo", "Dem. Rep. Congo"],
  ["Dominican Republic", "Dominican Rep."],
  ["Equatorial Guinea", "Eq. Guinea"],
  ["Iran (Islamic Republic of)", "Iran"],
  ["Lao People's Democratic Republic", "Laos"],
  ["Marshall Islands", "Marshall Is."],
  ["Micronesia (Federated States of)", "Micronesia"],
  ["Republic of Korea", "South Korea"],
  ["Republic of Moldova", "Moldova"],
  ["Russian Federation", "Russia"],
  ["Saint Kitts and Nevis", "St. Kitts and Nevis"],
  ["Saint Vincent and the Grenadines", "St. Vin. and Gren."],
  ["Sao Tome and Principe", "So Tom and Principe"],
  ["Solomon Islands", "Solomon Is."],
  ["South Sudan", "S. Sudan"],
  ["Swaziland", "eSwatini"],
  ["Syrian Arab Republic", "Syria"],
  ["The former Yugoslav Republic of Macedonia", "Macedonia"],
  // ["Tuvalu", ?],
  ["United Republic of Tanzania", "Tanzania"],
  ["Venezuela (Bolivarian Republic of)", "Venezuela"],
  ["Viet Nam", "Vietnam"]
])

const projection = d3.geoEqualEarth()
const path = d3.geoPath(projection)
const countries = topojson.feature(world, world.objects.countries)
const outline = { type: "Sphere" } as ExtendedGeometryCollection<GeoGeometryObjects>

interface Props {
  data: DItem[]
  focused?: Array<HintData | OpNode>
  allDataPhrases?: Array<Array<HintData | OpNode>>
}

export const MapVis = React.memo((props: Props) => {
  const focused = useCompare(props.focused ?? [])
  const cacheData = useCompare(props.data ?? []) //useMemo(() => getLastWeekByCountry(props.data ?? []), [props.data])

  const allDataPhrases = useCompare(props.allDataPhrases ?? [])

  const [dataType, setDataType] = useState<'cases' | 'deaths' | null>(null)
  const [totalData, setTotalData] = useState<'total' | 'this_week' | null>(null)
  const [hlRegionOptions, setHLRegionOptions] = useState<string[] | null>(null)
  const [hlRegion, setHLRegion] = useState<string | null>(null)

  const { data } = useMemo(() => {

    const focusedRegions = hlRegion === null
      ? focused.filter(d => d.attr === 'region' && d.dataType === 'value').map(d => d.value)
      : hlRegion === 'all' ? [] : [hlRegion]

    const data = new Map(cacheData.map(d => ([
      rename.get(d.data.country) || d.data.country as string,
      { ...d, focuse: focusedRegions.length === 0 ? true : focusedRegions.includes(d.data.region) }
    ])))
    return { data, focusedRegions }
  }, [cacheData, focused, hlRegion])

  useEffect(() => {
    const flatDataPhrases = allDataPhrases.flatMap(d => d)
    const dataType = flatDataPhrases.find(d => d.value === 'new_deaths' || d.value === 'total_deaths')
    setDataType(dataType ? 'deaths' : null)

    const totalData = flatDataPhrases.find(d => d.value === 'total_deaths' || d.value === 'total_cases')
    setTotalData(totalData ? 'total' : null)

    const highlightedRegions = uniq(flatDataPhrases.filter(d => d.attr === 'region' && d.dataType === 'value').map(d => d.value))

    if (!isEqual(highlightedRegions, hlRegionOptions)) {
      console.log('setHLRegionOptions')
      setHLRegionOptions(highlightedRegions.length ? highlightedRegions : null)
    }
    setHLRegion(highlightedRegions.length ? highlightedRegions[highlightedRegions.length - 1] : null)
  }, [allDataPhrases, hlRegionOptions])

  const color = useMemo(() => d3.scaleSequentialSymlog()
    .domain(d3.extent(
      Array.from(data.values()).map(d => d.data.new_cases)
    ) as [number, number])
    .interpolator(d3.interpolateYlGnBu)
    .unknown("#ccc"), [data])

  const height = useMemo(() => {
    const [[x0, y0], [x1, y1]] = d3.geoPath(projection.fitWidth(width, outline)).bounds(outline);
    const dy = Math.ceil(y1 - y0), l = Math.min(Math.ceil(x1 - x0), dy);
    projection.scale(projection.scale() * (l - 1) / l).precision(0.2);
    return dy;
  }, [])

  return <div className="mapWrapper">
    <div className="controls" style={{ marginBottom: '20px' }}>

      {totalData &&
        <ToggleButtonGroup
          size="small"
          exclusive
          aria-label="data-basis"
          value={totalData}
          onChange={(_, v) => setTotalData(v)}
        >
          <ToggleButton value="this_week" aria-label="left aligned">
            {"This Week"}
          </ToggleButton>
          <ToggleButton value="total" aria-label="centered">
            {"Total"}
          </ToggleButton>
        </ToggleButtonGroup>
      }


      {dataType &&
        <ToggleButtonGroup
          size="small"
          exclusive
          aria-label="data-basis"
          value={dataType}
          onChange={(_, v) => setDataType(v)}
        >
          <ToggleButton value="cases" aria-label="left aligned">
            {"Cases"}
          </ToggleButton>
          <ToggleButton value="deaths" aria-label="centered">
            {"Deaths"}
          </ToggleButton>
        </ToggleButtonGroup>
      }

      {hlRegionOptions &&
        <FormControl style={{ minWidth: '80px' }} size="small" variant='outlined'>
          <InputLabel id="select-outlined-label">Region</InputLabel>
          <Select
            labelId="select-outlined-label"
            id="simple-select-outlined"
            value={hlRegion}
            onChange={(e) => {
              console.log('highlight region', e.target.value)
              const value: string = e.target.value as string
              setHLRegion(value)
            }}
            displayEmpty
            label="Region"
          >
            <MenuItem value={'all'}>All</MenuItem>
            {hlRegionOptions.map(r => <MenuItem key={r} value={r}>{r}</MenuItem>)}
          </Select>
        </FormControl>
      }
    </div>
    <svg display='block' viewBox={`0 0 ${width} ${height}`} className={'map'}>
      <defs>
        <path id="outline" d={path(outline)!} />
        <clipPath id="clip">
          <use xlinkHref={new URL("#outline", window?.location.href).href}></use>
        </clipPath>
      </defs>
      <g clipPath={`url(${new URL("#clip", window.location.href)})`}>
        <use xlinkHref={new URL("#outline", window.location.href).href} fill="white"></use>
        {countries.features.map(d => {
          const attr = (totalData === 'total' && dataType === 'cases')
            ? 'total_cases'
            : (totalData === 'total' && dataType === 'deaths')
              ? 'total_deaths'
              : dataType === 'deaths'
                ? 'new_deaths'
                : 'new_cases'
          return <path key={d.properties.name}
            fill={color(data.get(d.properties.name)?.data[attr])}
            d={path(d)!}
            opacity={data.get(d.properties.name)?.focuse ? 1 : 0.2}
            className="country"
          >
            <title>{`${d.properties.name} ${data.has(d.properties.name) ? data.get(d.properties.name)?.data[attr] : "N/A"}`}
            </title>
          </path>
        })}
        <path fill='none' stroke='white' strokeLinejoin="round"
          d={path(topojson.mesh(world, world.objects.countries, (a, b) => a !== b))!}>
        </path>
      </g>

      <use xlinkHref={new URL("#outline", window?.location.href).href} fill="none" stroke='black'>
      </use>
    </svg>
  </div>
})
MapVis.displayName = 'MapVis'