import { useCallback, useMemo, useState, KeyboardEvent, useEffect } from 'react';
import { Editor, Range, Transforms, BasePoint } from 'slate';
import {
  isCollapsed,
  isPointAtWordEnd,
  getRenderElement,
  getPlatePluginTypes,
  OnChange,
  // KeyboardHandler,
  PlatePlugin,
  SPEditor,
  useEventEditorId, useStoreEditorRef,
} from '@udecode/plate';
import { cloneDeep, isEqual } from 'lodash';

import { insertHint } from './insertHint';
import {
  getPreviousIndex, getNextIndex, NLPParser, addVisData, getCurrentSentence, addHintData, getHintNode, getDataPhrases
} from './utils';
import { stringSearch, newTaskEngine, ITaskNode } from './taskEngine'
import { getHintDeserialize } from './getHintDeserialize';
import { reactiveUpdate } from './reactiveUpdate';

import {
  GetHintSelectProps,
  HintData,
  HintPluginOptions,
  ELEMENT_HINT,
  PlaceholderHintData,
  Others,
} from './types';
import { ConstituencyNode, NLPLabel } from '../../../@types';

import { useCompare } from '../../../App';
import { OpNode } from '../operator';

type KeyboardHandler<T extends SPEditor = SPEditor> = (editor: T) => (event: KeyboardEvent<Element>) => Promise<void | false>

type HintPluginReturn = { plugin: PlatePlugin; getHintSelectProps: () => GetHintSelectProps; searchValue: string; }

const pluginKey = ELEMENT_HINT

export const useAutoHintPlugin = ({
  // dataTable, 
  rawData,
  onTab, onSelectHint, onFocusCurSentence, onOthers, onChangeDataPhrases
}: HintPluginOptions): HintPluginReturn => {
  // dataTable = useCompare(dataTable)
  rawData = useCompare(rawData)

  const [targetRange, setTargetRange] = useState<Range | null>(null);
  const [valueIndex, setValueIndex] = useState(0);
  const [curTokens, setCurTokens] = useState<{
    search?: string,
    nlpTokens: NLPLabel[],
    constituency: ConstituencyNode | null
    tab: boolean,
  }>({ search: '', nlpTokens: [], constituency: null, tab: false })
  const [curDataPhrases, setCurDataPhrases] = useState<
    { curIdx: number, sentences: Array<Array<HintData | OpNode>> }>({ curIdx: -1, sentences: [] })

  const editor = useStoreEditorRef(useEventEditorId('focus'));

  // here must evaluate the value
  const { candidates: hintCandidates, taskTree, others } = useMemo(
    () => {
      if (!targetRange || !curTokens) return { candidates: [] }
      const { nlpTokens, constituency, search, tab } = curTokens

      // how to lock dataset?
      const hintNodes = nlpTokens.filter(t => t.hintPath).map(t => getHintNode(t, editor!))
      console.log('hintNode', hintNodes, 'nlpTokens', nlpTokens)
      let datasetKey
      if (hintNodes.some(h => h?.datasetId)) {
        datasetKey = hintNodes.find(h => h?.datasetId)?.datasetId
      }
      console.log('=====> datasetKey', datasetKey)

      console.group('Update hintCandidates')
      // auto-hint, string match
      let candidates: HintData[] = []
      let taskTree: ITaskNode | undefined
      let others: Others | undefined
      if (search) {
        console.log('search text:', search)
        candidates = stringSearch(search, rawData, datasetKey)
      } else {
        // no search text, thus the system should give a computed value
        console.log('NLPLabels', curTokens)
        // if curTokens contains countries, load another dataset
        const result = newTaskEngine(nlpTokens, constituency, rawData[datasetKey], rawData, editor!)
        candidates = result.candidates.map(d => ({ ...d, datasetId: datasetKey }))
        taskTree = result.taskTree as ITaskNode
        others = result.others
      }

      if (tab && candidates.length === 1 && candidates[0].dataType === 'value' && isFinite(+candidates[0].value)) {
        candidates.push({
          rowId: '_null',
          nodeType: 'placeholder',
          phType: 'RATIO',
          value: 'RATIO',
          dataType: 'computed',
          attr: 'TBD',
          colIdx: -1,
          rowIdx: -1,
          deps: null
        })
      }

      // if no candidates, proivde some placeholder for the user
      if (tab && candidates.length === 0) {
        // candidates.push()
        candidates.push({
          rowId: '_null',
          nodeType: 'placeholder',
          phType: 'VALUE',
          value: 'VALUE',
          dataType: 'value',
          attr: 'TBD',
          colIdx: -1,
          rowIdx: -1,
          deps: null,
        }, {
          rowId: '_null',
          nodeType: 'placeholder',
          phType: 'COUNT_ITEMS',
          value: 'COUNT_ITEMS',
          dataType: 'computed',
          attr: 'TBD',
          colIdx: -1,
          rowIdx: -1,
          deps: null
        }, {
          rowId: '_null',
          nodeType: 'placeholder',
          phType: 'RATIO',
          value: 'RATIO',
          dataType: 'computed',
          attr: 'TBD',
          colIdx: -1,
          rowIdx: -1,
          deps: null
        }, {
          rowId: '_null',
          nodeType: 'placeholder',
          phType: 'DIFF_ATTR',
          value: 'DIFF_ATTR',
          dataType: 'computed',
          attr: 'TBD',
          colIdx: -1,
          rowIdx: -1,
          deps: null
        })
      }

      console.groupEnd()
      return { candidates, taskTree, others }
    },
    [curTokens, targetRange, editor, rawData]
  )

  /**
   * Update the highlighted cell
   */
  useEffect(() => {
    // console.group('useEffect onSelectHint')
    const candidate = hintCandidates[valueIndex]
    onSelectHint?.(candidate, taskTree, others)
    // console.groupEnd()
  }, [valueIndex, hintCandidates, taskTree, others, onSelectHint])

  const onKeyDownHint: KeyboardHandler = useCallback((editor) => async (e: KeyboardEvent) => {
    // a bug here
    if (targetRange && hintCandidates.length) {
      switch (e.key) {
        case 'ArrowDown': {
          e.preventDefault();
          const newIdx = getNextIndex(valueIndex, hintCandidates.length - 1)
          return setValueIndex(newIdx);
        }
        case 'ArrowUp': {
          e.preventDefault();
          const newIdx = getPreviousIndex(valueIndex, hintCandidates.length - 1)
          return setValueIndex(newIdx);
        }
        case 'Escape': {
          e.preventDefault();
          return setTargetRange(null);
        }
        case 'Tab':
        case 'Enter': {
          e.preventDefault();
          console.group('To Insert Hints')
          // insert
          hintCandidates[valueIndex].selectedIdx = valueIndex
          if (targetRange !== null) {
            Transforms.select(editor, targetRange);
            insertHint(editor, hintCandidates[valueIndex]);
          }

          // after insert


          // 2. update the placeholder
          if (curTokens) {
            let { nlpTokens, constituency } = curTokens
            if (!curTokens.tab) {
              const { selection } = editor;
              if (!selection) {
                console.warn('No selection after insert the hint!')
                return false
              }
              console.log(editor.children)
              const [sentenceIdx, sentences] = getCurrentSentence(selection, editor)
              // a hack
              const sentence = sentences[sentences.length - 1]
              // 3. parse the sentence
              const { nlpTokens: newNlpTokens, constituency: newConstituency } = await NLPParser('_placeholder', sentence)
              // merge with hintData
              const nlpLabels = addHintData(editor, newNlpTokens, sentence)

              if (!isEqual(curTokens.constituency, newConstituency)) {
                setCurTokens({
                  ...curTokens,
                  nlpTokens: nlpLabels,
                  constituency: newConstituency, tab: true,
                })
                nlpTokens = nlpLabels
                constituency = newConstituency
              }
            }

            // get all placeholder
            const placeHolder = nlpTokens?.reduce((o, t) => {
              const hintNode = getHintNode(t, editor)
              return hintNode?.nodeType === 'placeholder'
                ? { token: t, hintNode }
                : o
            }, null as null | { token: NLPLabel, hintNode: PlaceholderHintData })
            console.log('placeHolder', placeHolder)

            if (placeHolder) {
              const vTokens = cloneDeep(nlpTokens)
              vTokens![vTokens.length - 1].hintPath = [0, editor.children[0].children.length - 2]
              console.log('vTokens', vTokens)

              let datasetKey
              const hintNodes = vTokens.filter(t => t.hintPath).map(t => getHintNode(t, editor!))
              if (hintNodes.some(h => h?.datasetId)) {
                datasetKey = hintNodes.find(h => h?.datasetId)?.datasetId
              }
              console.log('datasetKey for placeholder update', datasetKey)

              // update the value
              const { returnNode, others } = reactiveUpdate(vTokens!, constituency,
                placeHolder.hintNode, placeHolder.token.hintPath!,
                rawData[datasetKey], rawData, editor)

              if (returnNode) {
                setImmediate(() => {
                  others && onOthers?.(others, returnNode?.valueFormat)
                })
              }
            }
          }

          // should update the curDataPhrases
          const curSentenceDPs = curDataPhrases.sentences[curDataPhrases.curIdx] ?? []
          if (curDataPhrases.curIdx === -1) {
            console.warn('The idx of curDataPhrases should not be -1!')
          }
          // update the dataPhrases
          const newCurDataPhrases = {
            ...curDataPhrases,
            sentences: [...curDataPhrases.sentences]
          }
          newCurDataPhrases.sentences[curDataPhrases.curIdx] = [...curSentenceDPs, hintCandidates[valueIndex]]
          setCurDataPhrases(newCurDataPhrases)

          const dataPhrases = newCurDataPhrases.sentences
          onFocusCurSentence?.(dataPhrases[curDataPhrases.curIdx])
          onChangeDataPhrases?.(dataPhrases.filter(d => d !== undefined && d.length !== 0) as Array<Array<HintData | OpNode>>)

          setTargetRange(null);
          console.log('finish insertHint')
          console.groupEnd()
          return false;
        }
      }
    } else {
      if (e.key === 'Tab') {
        e.preventDefault()
        console.group('Tab fetch')

        const { selection } = editor;
        if (selection && isCollapsed(selection)) {
          const cursor = Range.start(selection);

          // Point at the start of line
          const { mentionRange, afterMatchText } = matchTheLastSpace(editor, cursor);

          if (isPointAtWordEnd(editor, { at: cursor })) {
            // 2. find the current sentence
            const [sentenceIdx, sentences] = getCurrentSentence(selection, editor)
            const sentence = sentences[sentenceIdx]
            // 3. parse the sentence
            const { nlpTokens, constituency } = await NLPParser(afterMatchText, sentence)
            // merge with hintData
            const nlpLabels = addHintData(editor, nlpTokens, sentence)

            console.log('sentence', sentence)
            console.log('To set:', 'afterMatchText', afterMatchText)
            console.log('NlpLabels', nlpLabels, 'nlpTokens', nlpTokens)
            console.log('Constituency', constituency)
            setValueIndex(0);
            if (afterMatchText !== curTokens.search || !isEqual(curTokens.constituency, constituency)) {
              setCurTokens({
                search: afterMatchText,
                nlpTokens: afterMatchText ? nlpLabels : [...nlpLabels, { ...nlpTokens.pop()! }],
                constituency, tab: true,
              })
            }
            setTargetRange(mentionRange as Range);

            // for visualizing the dependencies, merge with bbox
            const visLabels = addVisData(editor, nlpTokens, sentence)
            onTab?.(visLabels, nlpLabels, constituency)

            console.groupEnd()
            return false;
          }

          setTargetRange(null);
          console.groupEnd()
        }
      }
    }
  }, [targetRange, valueIndex, hintCandidates, curTokens, curDataPhrases, onFocusCurSentence, onChangeDataPhrases, rawData, onOthers, onTab]
  );

  /**
   * typing the words
   */
  const onChange: OnChange = useCallback((editor) => (inputValue) => {
    console.log('useHint onChange<====', inputValue)
    const { selection } = editor;
    if (!(selection && isCollapsed(selection))) return

    // if already in hint
    const cursor = Range.start(selection);
    // Point at the start of line
    const { mentionRange, afterMatchText } = matchTheLastSpace(editor, cursor);
    if (isPointAtWordEnd(editor, { at: cursor }) && afterMatchText.length > 1) {
      console.log('afterMatchText', afterMatchText)
      setTargetRange(mentionRange as Range);
      setCurTokens({
        ...curTokens,
        // nlpTokens: [], constituency: null,
        search: afterMatchText, tab: false,
      })
      setValueIndex(0);
    } else {
      setTargetRange(null);
    }

    // 1. find the current sentence
    let [newCurSentenceIdx, sentences] = getCurrentSentence(selection, editor)
    if (selection.anchor.path[0] === 0 && selection.anchor.path[1] === editor.children[0].children.length - 1) {
      // ===>should be the last sentence
      console.log('hack to the last sentence')
      console.log('newCurSentenceIdx:', newCurSentenceIdx, 'sentences:', sentences)
      newCurSentenceIdx = sentences.length - 1
    }

    // if the last sentence end with period, should start a new sentence
    if (sentences[newCurSentenceIdx]?.sentence.trimEnd().endsWith('.')) {
      newCurSentenceIdx += 1
    }

    // get the current data phrase
    const newDataPhrases = sentences.map(sentence => getDataPhrases(sentence, editor))
    // use this dataPhrase to calculate which cells should be highlighted
    const newCurDataPhrases = {
      curIdx: newCurSentenceIdx,
      sentences: newDataPhrases
    }
    console.log('newCurDataPhrases', newCurDataPhrases)
    // if all is different
    const diffAll = !isEqual(newDataPhrases, curDataPhrases.sentences)
    const diffCur = !isEqual(newDataPhrases[newCurSentenceIdx], curDataPhrases.sentences[curDataPhrases.curIdx])
    if (diffAll) {
      onChangeDataPhrases?.(newDataPhrases.filter(d => d !== undefined && d.length !== 0) as Array<Array<HintData | OpNode>>)
    }

    if (newCurSentenceIdx !== curDataPhrases.curIdx) {
      // change sentence, should clean the curTokens
      console.log('========+> change sentence', newCurSentenceIdx)
      setCurTokens({
        ...curTokens,
        nlpTokens: [], constituency: null,
      })
    }

    if (diffCur) {
      // console.log('onChange --> onFocusCurSentence')
      onFocusCurSentence?.(newDataPhrases[newCurSentenceIdx])
    }
    //  if either all or curIdx is differnt
    if (diffAll || diffCur) {
      console.log('setCurDataPhrases')
      setCurDataPhrases(newCurDataPhrases)
    }
  }, [curDataPhrases, curTokens, onFocusCurSentence, onChangeDataPhrases])

  return {
    plugin: useMemo(
      () => ({
        pluginKeys: pluginKey,
        onChange,
        renderElement: getRenderElement(pluginKey),
        onKeyDown: onKeyDownHint,
        deserialize: getHintDeserialize(pluginKey),
        inlineTypes: getPlatePluginTypes(pluginKey),
        voidTypes: getPlatePluginTypes(pluginKey),
      }) as any,
      [onChange, onKeyDownHint]
    ),

    getHintSelectProps: useCallback(
      () => ({
        at: targetRange,
        valueIndex,
        options: hintCandidates,
      }),
      [targetRange, valueIndex, hintCandidates]
    ),
    searchValue: curTokens.search || '',
  };
}

function matchTheLastSpace(editor: SPEditor, cursor: BasePoint) {
  const lineStart = Editor.before(editor, cursor, { unit: 'line' });
  // Range from before to start
  const beforeRange = lineStart && Editor.range(editor, lineStart, cursor);
  // Before text
  const beforeText = (beforeRange && Editor.string(editor, beforeRange)) ?? '';
  // Match regex on before text
  const match = !!beforeText && beforeText.match(/\s(?=\S*$)/);
  const afterMatchText = (match && typeof match?.index === 'number')
    ? match.input?.slice(match?.index + 1) ?? ''
    : '';
  // Point at the start of mention
  const mentionStart = afterMatchText.length === 0
    ? cursor
    : Editor.before(editor, cursor, {
      unit: 'character',
      distance: afterMatchText.length,
    });
  // Range from mention to start
  const mentionRange = mentionStart && Editor.range(editor, mentionStart, cursor);
  return { mentionRange, afterMatchText };
}
