import { chunk, isFinite } from 'lodash'
import { DItem } from "../../../../@types"
import {
    abs,
    allItem, avg, debugLog, largerThanItem, lastUnit, lessThanItem, maxItem, minItem, sum
} from "../utils"

/**
 * Operator nodes
 */
export enum OP_TYPE {
    EXTREME = 'extreme',
    FILTER = 'comparefilter',
    COMPUTED = 'computed',
    CONST_NUMBER = 'constnumber',
    COMPARE = 'compare',
    RANGE = 'range',
    DISTRIBUTE = 'distribute',
    ADD_UP = 'addup',
    ABS = 'abs'
}

/** 
 * Extremem Op
 */
export type ExtremumOp = <T extends DItem>(arr: T[], key: string) => T
export interface ExtremeNode { type: OP_TYPE.EXTREME, op: ExtremumOp, name?: string }
const ExtremeMaxOp = (): ExtremeNode => ({ type: OP_TYPE.EXTREME, op: maxItem, name: 'max' })
const ExtremeMinOp = (): ExtremeNode => ({ type: OP_TYPE.EXTREME, op: minItem, name: 'min' })
const matchExtremeMaxOp = (keyword: string = '', tag: string = '') => ['maximum', 'highest', 'greatest', 'largest', 'biggest', 'most', 'best', 'heaviest'].map(key => {
    const regEx = new RegExp(key, 'i')
    const matchRes = keyword.match(regEx)
    const matchIdx = matchRes?.index ?? -1
    return { matchIdx, opKey: key as string, type: OP_TYPE.EXTREME }
}).filter(({ matchIdx }) => matchIdx !== -1)
const matchExtremeMinOp = (keyword: string = '', tag: string = '') => ['minimum', 'smallest', 'lowest', 'least', 'worst', 'lightest'].map(key => {
    const regEx = new RegExp(key, 'i')
    const matchRes = keyword.match(regEx)
    const matchIdx = matchRes?.index ?? -1
    return { matchIdx, opKey: key as string, type: OP_TYPE.EXTREME }
}).filter(({ matchIdx }) => matchIdx !== -1)


