const FilterMoreOp = (): FilterNode => ({ type: OP_TYPE.FILTER, op: largerThanItem })
const FilterAllOp = (): FilterNode => ({ type: OP_TYPE.FILTER, op: allItem })
const FilterLastOp = (oData: string): FilterNode => ({ type: OP_TYPE.FILTER, op: lastUnit, oData })
