/**
 * Abs Op
 */
export type AbsOp = (value: number) => number
export interface AbsOpNode { type: OP_TYPE.ABS, op: AbsOp }
const AbsOp = (): AbsOpNode => ({ type: OP_TYPE.ABS, op: (a) => -1 * +a })
const matchAbs = (keyword: string = '', tag: string = '') => ['reduce', 'decrease'].map(key => {
    const regEx = new RegExp(key, 'i')
    const matchRes = keyword.match(regEx)
    const matchIdx = matchRes?.index ?? -1
    return { matchIdx, opKey: key as string, type: OP_TYPE.ABS }
}).filter(({ matchIdx }) => matchIdx !== -1)


/**
 * Compare Op
 */
export type CompareOp = <T>(a: T, b: T) => number
export interface CompareNode { type: OP_TYPE.COMPARE, op: CompareOp }
const CompareDiffOp = (): CompareNode => ({
    type: OP_TYPE.COMPARE,
    op: <T>(a: T, b: T) => {
        if (isFinite(+a) && isFinite(+b)) {
            return +a - +b
        }
        return 0
    }
})
const CompareRatioOp = (): CompareNode => ({
    type: OP_TYPE.COMPARE,
    op: <T>(a: T, b: T) => {
        if (isFinite(+a) && isFinite(+b)) {
            return +a
        }
        return 0
    }
})
const matchCompareOp = (keyword: string = '', tag: string = '') => ['compare'].map(key => {
    const regEx = new RegExp(key, 'i')
    const matchRes = keyword.match(regEx)
    const matchIdx = matchRes?.index ?? -1
    return { matchIdx, opKey: key as string, type: OP_TYPE.COMPARE }
}).filter(({ matchIdx }) => matchIdx !== -1)


/**
 * Range Op
 */
export type RangeOp = any
export interface RangeNode { type: OP_TYPE.RANGE, op: RangeOp }
const RangeStartOp = (): RangeNode => ({
    type: OP_TYPE.RANGE, op: (items: DItem[], attr: string) => {

    }
})
const RangeEndOp = (): RangeNode => ({ type: OP_TYPE.RANGE, op: () => { } })
const matchRangeStartOp = (keyword: string = '', tag: string = '') => [/\bfrom\b/].map(key => {
    const regEx = new RegExp(key, 'i')
    const matchRes = keyword.match(regEx)
    const matchIdx = matchRes?.index ?? -1
    return { matchIdx, opKey: 'from' as string, type: OP_TYPE.RANGE }
}).filter(({ matchIdx }) => matchIdx !== -1)
const matchRangeEndOp = (keyword: string = '', tag: string = '', head_text: string = '') => [/\bto\b/].map(key => {
    const regEx = new RegExp(key, 'i')
    const matchRes = keyword.match(regEx)
    let matchIdx = matchRes?.index ?? -1
    // no head_text now
    matchIdx = ['from', 'decline', 'increase'].some(s => head_text.toLowerCase().includes(s)) ? matchIdx : -1
    return { matchIdx, opKey: 'to' as string, type: OP_TYPE.RANGE }
}).filter(({ matchIdx }) => matchIdx !== -1)

/**
 * Distribute Op
 */
export type DistributeOp = any
export interface DistributeNode { type: OP_TYPE.DISTRIBUTE, op: DistributeOp }
const DistributeDecreseOp = (): DistributeNode => ({
    type: OP_TYPE.DISTRIBUTE, op: (items: DItem[], value: any, attr: string) => {
        debugLog('DistributeDecreseOp', items, value, attr)
        console.log('distribute', items)
        // filter the rows that decline in `attr`
        // desending by default
        const getValue = item => value === '_all' ? item.data[attr] : item.data[value][attr]
        return items.filter((item, itemIdx) => {
            const filterValue = getValue(item)
            const latterThan = items.filter((_, idx) => idx < itemIdx)
            // not continue, but all decline
            return latterThan.length > 0 ? latterThan.every(t => getValue(t) < filterValue) : false
        })
    }
})
const DistributeIncreaseOp = (): DistributeNode => ({
    type: OP_TYPE.DISTRIBUTE, op: (items: DItem[], value: any, attr: string) => {
        debugLog('DistributeIncreaseOp', items, value, attr)
        console.log('distribute', items)
        // filter the rows that decline in `attr`
        // desending by default
        const getValue = item => value === '_all' ? item.data[attr] : item.data[value][attr]
        return items.filter((item, itemIdx) => {
            const filterValue = getValue(item)
            const latterThan = items.filter((_, idx) => idx > itemIdx)
            // not continue, but all decline
            return latterThan.length > 0 ? latterThan.every(t => getValue(t) > filterValue) : false
        })
    }
})
const matchDistributeDecreseOp = (keyword: string = '', tag: string = '') => ['decline'].map(key => {
    const regEx = new RegExp(key, 'i')
    const matchRes = keyword.match(regEx)
    const matchIdx = matchRes?.index ?? -1
    return { matchIdx, opKey: key as string, type: OP_TYPE.DISTRIBUTE }
}).filter(({ matchIdx }) => matchIdx !== -1)
const matchDistributeIncreaseOp = (keyword: string = '', tag: string = '') => ['increase'].map(key => {
    const regEx = new RegExp(key, 'i')
    const matchRes = keyword.match(regEx)
    const matchIdx = matchRes?.index ?? -1
    return { matchIdx, opKey: key as string, type: OP_TYPE.DISTRIBUTE }
}).filter(({ matchIdx }) => matchIdx !== -1)

/**
 * AddUp Op
 */
export type AddupOp = (items: DItem[]) => DItem[]
export interface AddupNode { type: OP_TYPE.ADD_UP, oData: string, op: AddupOp }
const matchAddupOp = (keyword: string = '', tag: string = '') => ['week'].map(key => {
    const regEx = new RegExp(key, 'i')
    const matchRes = keyword.match(regEx)
    const matchIdx = matchRes?.index ?? -1
    return { matchIdx, opKey: key as string, type: OP_TYPE.ADD_UP }
}).filter(({ matchIdx }) => matchIdx !== -1)

/**
 * ConstNum Op
 */
export type ConstNumOp = () => number
export interface ConstNumNode { type: OP_TYPE.CONST_NUMBER, op: ConstNumOp }
const matchConstNum = (keyword: string = '', tag: string = '') => [/\d+/].map(key => {
    const regEx = new RegExp(key, 'i')
    const matchRes = keyword.match(regEx)
    const matchIdx = matchRes?.index ?? -1
    return { matchIdx, opKey: ((matchRes?.[0] ?? '') as string).toLowerCase(), type: OP_TYPE.CONST_NUMBER }
}).filter(({ matchIdx }) => matchIdx !== -1)
// ['CD'].includes(tag) || ['week'].includes(keyword)

export type IOpNode = ExtremeNode | FilterNode | ComputedNode | ConstNumNode | CompareNode | RangeNode | DistributeNode | AddupNode | AbsOpNode

const isOpNode = (n: any): n is IOpNode =>
    typeof n === 'object'
    && 'op' in n
    && typeof n['op'] === 'function'
    && 'type' in n
    && (Object.keys(n).length === 2 || Object.keys(n).length === 3)

const build = {
    ExtremeMaxOp,
    ExtremeMinOp,
    FilterLessOp,
    FilterMoreOp,
    FilterAllOp,
    FilterLastOp,
    ComputeSumOp,
    ComputeAvgOp,
    AbsOp,
    CompareDiffOp,
    CompareRatioOp,
    RangeStartOp,
    RangeEndOp,
    DistributeDecreseOp,
    DistributeIncreaseOp,
    AddupNode: (interval: string | number, oData: string): AddupNode => ({
        type: OP_TYPE.ADD_UP, oData, op: (items: DItem[]) => {
            interval = interval.toString().includes('week') ? 7 : +interval
            const chunkItems = chunk(items, interval)
            return chunkItems.map((chunk, idx) => {

                const data = chunk.reduce((o, item) => {
                    if (Object.keys(o).length === 0) {
                        o = { ...item.data }
                    } else {
                        Object.keys(item.data)
                            .forEach(k => {
                                if (k !== 'date') o[k] += item.data[k]
                            })
                    }
                    return o
                }, {} as Record<string, any>)

                return { id: data['date'], rowIdx: idx, virtualAttr: [], data } as DItem
            })
        }
    }),
    ConstNum: (value: string): ConstNumNode => ({
        type: OP_TYPE.CONST_NUMBER, op: () => {
            if (isFinite(+value)) return +value
            else if (value === 'week') return 7
            return 0
        }
    })
}

const match = {
    ExtremeMaxOp: matchExtremeMaxOp,
    ExtremeMinOp: matchExtremeMinOp,
    filterLessOp,
    filterMoreOp,
    filterAllOp,
    filterLastOp,
    ComputeAvg: matchComputeAvg,
    CompareOp: matchCompareOp,
    ComputeSum: matchComputeSum,
    Abs: matchAbs,
    rangeStart: matchRangeStartOp,
    rangeEnd: matchRangeEndOp,
    distributeDecrese: matchDistributeDecreseOp,
    distributeIncrease: matchDistributeIncreaseOp,
    addupOp: matchAddupOp,
    ConstNum: matchConstNum,
}

const is = {
    Op: isOpNode,
    ExtremeOp: (n: any): n is ExtremeNode => isOpNode(n) && n.type === OP_TYPE.EXTREME,
    FilterOp: (n: any): n is FilterNode => isOpNode(n) && n.type === OP_TYPE.FILTER,
    ComputedOp: (n: any): n is ComputedNode => isOpNode(n) && n.type === OP_TYPE.COMPUTED,
    ConstNumOp: (n: any): n is ConstNumNode => isOpNode(n) && n.type === OP_TYPE.CONST_NUMBER,
    CompareOp: (n: any): n is CompareNode => isOpNode(n) && n.type === OP_TYPE.COMPARE,
    RangeOp: (n: any): n is RangeNode => isOpNode(n) && n.type === OP_TYPE.RANGE,
    DistributeOp: (n: any): n is DistributeNode => isOpNode(n) && n.type === OP_TYPE.DISTRIBUTE,
    AddupOp: (n: any): n is AddupNode => isOpNode(n) && n.type === OP_TYPE.ADD_UP,
    AbsOp: (n: any): n is AbsOpNode => isOpNode(n) && n.type === OP_TYPE.ABS
}

// eslint-disable-next-line import/no-anonymous-default-export
export default {
    build, match, is
}