
import {
    getPlatePluginTypes,
    getRenderElement,
    PlatePlugin,
} from '@udecode/plate-core';
import { ELEMENT_OP, withLink } from './index';
import { getLinkDeserialize } from './getLinkDeserialize';
import { WithLinkOptions } from './types';

/**
 * Enables support for hyperlinks.
 */
export const createOPPlugin = (options?: WithLinkOptions): PlatePlugin => ({
    pluginKeys: ELEMENT_OP,
    renderElement: getRenderElement(ELEMENT_OP),
    deserialize: getLinkDeserialize(),
    inlineTypes: getPlatePluginTypes(ELEMENT_OP),
    withOverrides: withLink(options),
});