import './style.scss'
import React, { useContext, useEffect, useState } from 'react';
import { ReactEditor, useFocused, useSelected, useSlate } from 'slate-react';
import { Editor } from 'slate';
import clsx from 'clsx';

import { HintElementProps } from './types';
import { Dropdown, Option } from '../Select';

import { isDepHintNode, reactiveUpdate, setHintNode } from '../WordProcessor/autoHint';
import { DatatableContext } from '..';

export const HintElement = (props: HintElementProps) => {
  const {
    attributes,
    children,
    element: hintNode,
    nodeProps,
    onSelectOption,
    onMouseDown,
    onMouseUp,
    onClick,
    onDoubleClick,
  } = props

  const { rawData } = useContext(DatatableContext);
  const [dropDown, setDropDown] = useState<{ value: any, attr: string, options: Option[] }>()

  // can we evalute the hintNode here?
  const selected = useSelected();
  const focused = useFocused();
  const editor = useSlate()
  const path = ReactEditor.findPath(editor as any, hintNode)

  // const tokens = element.value.split(' ')
  // const targetIdx = tokens.findIndex(t => t.includes(element.query))
  useEffect(() => {
    const onMouseDown = (e: MouseEvent) => {
      setDropDown(undefined)
    }
    document.addEventListener('mousedown', onMouseDown)
    return () => {
      document.removeEventListener('mousedown', onMouseDown)
    }
  }, [])

  const toolTip = (hintNode.dataType === 'value' && hintNode.colIdx === 0)
    ? `Item: ${hintNode.value}`
    : hintNode.dataType === 'value'
      ? `Value of ${hintNode.attr}`
      : hintNode.dataType === 'computed'
        ? 'Computed Value'
        : `Attr: ${hintNode.attr}`

  return (
    <>
      <span
        id={`io_${hintNode.id}`}
        {...attributes}
        data-slate-value={hintNode.value}
        data-tooltip={toolTip} data-tooltip-position="top"
        className={clsx('slate-HintElement', { selected, focused }, hintNode.nodeType)}
        contentEditable={false}
        onMouseDown={(e) => onMouseDown?.(e, hintNode, path)}
        onMouseUp={(e) => onMouseUp?.(e, hintNode, path)}
        onDoubleClick={(e) => onDoubleClick?.(e, hintNode, path)}
        onClick={(e) => {
          onClick?.(e, hintNode, path)

          if (hintNode.nodeType === 'indep') {
            console.log('click on indep', hintNode)
            const dataTable = rawData[hintNode.datasetId]
            const options = {
              value: hintNode.value,
              attr: hintNode.attr!,
              options: hintNode.dataType === 'value'
                ? dataTable.map((d, idx) => ({ value: d.data[hintNode.attr!], idx }))
                  .reduce((o, d) => {
                    if (!o.some(t => t.value === d.value)) {
                      o.push(d)
                    }
                    return o
                  }, [] as Option[])
                : (hintNode.colIdx !== 0 && hintNode.attr !== 'region')
                  ? Object.keys(dataTable[0].data)
                    .map((key, idx) => ({ value: key, idx }))
                    .filter(({ idx }) => idx !== 0)
                  : []
            }
            if (options.options.length)
              setDropDown(options)
          }
        }}
        {...nodeProps}
      >
        {/* {renderLabel(element)} */}
        {hintNode.children[0].text}
        {/** must render this children */}
        {children}

        {dropDown && <div
          style={{ position: 'absolute', display: 'inline', left: 0, top: '20px' }}
          onMouseDown={e => e.stopPropagation()}
        >
          <Dropdown
            value={dropDown.value}
            options={dropDown.options}
            onChange={newOption => {
              console.group('Select From Dropdown ===>')
              console.debug('newOption:', newOption)
              let returnNode, others
              if (hintNode.dataType === 'value') {
                console.group('Change a value hintNode')
                const dataTable = rawData[hintNode.datasetId]
                const newData = dataTable[newOption.idx]

                console.group('Update the value of the hintNode')
                console.debug('newData', newData, 'path', path, newData.data[hintNode.attr])
                setHintNode(editor, {
                  rowIdx: newData.rowIdx,
                  rowId: newData.id,
                  value: newData.data[hintNode.attr]
                }, path)
                console.groupEnd()

                // should then trigger some reactive update
                console.group('Triger reactive update')
                for (const [node, nodePath] of Editor.nodes(editor, { at: [path[0]], match: isDepHintNode, voids: true })) {
                  // re-evaluate
                  console.log('hintNode re-evaluate', node)
                  if (!node.deps) {
                    console.warn('Dep node has no deps', node)
                    continue
                  }
                  const result = reactiveUpdate(node.deps.tokens, node.deps.constituency, node as any, nodePath, dataTable, rawData, editor as any)
                  returnNode = result.returnNode
                  others = result.others
                }
                console.groupEnd()

                console.groupEnd()
              } else if (hintNode.dataType === 'attr' && hintNode.colIdx !== 0) {
                console.group('Change a attr hintNode')
                const dataTable = rawData[hintNode.datasetId]
                const newAttr = Object.keys(dataTable[0].data)[newOption.idx]

                console.group('Update the attr of the hintNode')
                console.debug('newAttr', newAttr, 'path', path)
                setHintNode(editor, {
                  attr: newAttr,
                  value: newAttr,
                }, path)
                console.groupEnd()

                // should then trigger some reactive update
                console.group('Triger reactive update')
                for (const [node, nodePath] of Editor.nodes(editor, { at: [path[0]], match: isDepHintNode, voids: true })) {
                  // re-evaluate
                  console.log('hintNode re-evaluate', node)
                  if (!node.deps) {
                    console.warn('Dep node has no deps', node)
                    continue
                  }
                  const result = reactiveUpdate(node.deps.tokens, node.deps.constituency, node as any, nodePath, dataTable, rawData, editor as any)
                  returnNode = result.returnNode
                  others = result.others

                }
                console.groupEnd()
                console.groupEnd()
              }

              onSelectOption?.({ returnNode, others }, hintNode, path)
              setDropDown(undefined)
              console.groupEnd()
            }}
          >
          </Dropdown>
        </div>}
      </span>
    </>
  );
}

export * from './types'