import {
  getRangeBefore,
  getText,
  isCollapsed,
  unwrapNodes,
  wrapNodes
} from '@udecode/plate-common';
import {
  getPlatePluginType,
  SPEditor,
  WithOverride,
} from '@udecode/plate-core';
import { withRemoveEmptyNodes } from '@udecode/plate-normalizers';
import { Range, Location, Editor, Point } from 'slate';
import { ReactEditor } from 'slate-react';

import OpNode from '../autoHint/taskEngine/OpNode';
import { WithLinkOptions } from './types';

export const ELEMENT_OP = 'opElm';

function isOperator(input: string) {
  for (const matcher of Object.values(OpNode.match)) {
    const res = matcher(input)
    if (res.length > 0) {
      console.log('res[0]', res[0])
      return res[0]
    }
  }
  return { matchIdx: -1, opKey: undefined, type: undefined }
}

const wrapLink = (editor: SPEditor, { at, opKey, type }: { opKey: string; type: string; at?: Location }) => {
  wrapNodes(
    editor,
    {
      type: getPlatePluginType(editor, ELEMENT_OP),
      opKey, opType: type,
      children: [],
    },
    { at, split: true }
  );
}

const upsertLink = (editor: SPEditor, { opKey, start, type }: { opKey: string; type: string; start: Point; }) => {

  const pluginType = getPlatePluginType(editor, ELEMENT_OP);
  const newSelection = editor.selection as Range;

  unwrapNodes(editor, { at: start, match: { type: pluginType } });
  wrapLink(editor, { at: { anchor: start, focus: newSelection.focus }, opKey, type });
}

const upsertLinkIfValid = (editor: SPEditor, at: Location) => {
  const [start, end] = Editor.edges(editor, Editor.path(editor, at))

  const rangeFromBlockStart = Editor.range(editor, start, end) // getRangeFromBlockStart(editor, { at });
  const textFromBlockStart = getText(editor, rangeFromBlockStart);

  // console.log('rangeFromBlockStart', rangeFromBlockStart)
  // console.log('textFromBlockStart', textFromBlockStart)
  const { opKey, matchIdx, type } = isOperator(textFromBlockStart)

  // dirty hack, ignore the `to` after compare
  const preNode = Editor.previous(editor, { at: start })
  if (opKey === 'to' && preNode) {
    const text = preNode[0]['text'] as string
    if (text && text.toLowerCase().includes('compare')) {
      return false
    }
  }

  if (rangeFromBlockStart && opKey && type) {
    upsertLink(editor, { opKey, type, start: { ...rangeFromBlockStart.anchor, offset: matchIdx } });
    return true;
  }
}

/**
 * Insert space after a url to wrap a link.
 * Lookup from the block start to the cursor to check if there is an url.
 * If not found, lookup before the cursor for a space character to check the url.
 *
 */
export const withLink = ({
  rangeBeforeOptions = {
    matchString: [',', ' '],
    skipInvalid: true,
    afterMatch: true,
    multiPaths: true,
  },
}: WithLinkOptions = {}): WithOverride<ReactEditor & SPEditor> => (editor) => {
  const { insertText } = editor;

  const type = getPlatePluginType(editor, ELEMENT_OP);

  editor.insertText = (text) => {
    if ([' ', ','].includes(text) && isCollapsed(editor.selection)) {
      const selection = editor.selection as Range;

      if (upsertLinkIfValid(editor, selection)) {
        return insertText(text);
      }

      const beforeWordRange = getRangeBefore(editor, selection, rangeBeforeOptions);
      if (beforeWordRange) {
        const beforeWordText = getText(editor, beforeWordRange);
        const { opKey, matchIdx, type } = isOperator(beforeWordText)

        // dirty hack, ignore the `to` after compare
        const preNode = Editor.previous(editor, { at: beforeWordRange.anchor.path })
        if (opKey === 'to' && preNode) {
          const preText = getText(editor, preNode[1])
          if (preText && preText.toLowerCase().includes('compare')) {
            insertText(text);
            return false
          }
        }

        if (opKey && type) {
          upsertLink(editor, { opKey, type, start: { ...beforeWordRange.anchor, offset: matchIdx } });
        }
      }
    }

    insertText(text);
  };

  editor = withRemoveEmptyNodes({ type })(editor);

  return editor;
}

export * from './createLinkPlugin';
export * from './getLinkDeserialize';
export * from './types';