import { ConstituencyNode } from "../../../../@types";
import { HintNode, isHintNode } from "../types";

export interface ConstituHintNode {
    hintNode: HintNode
    node: ConstituencyNode
}

export const isConstituHintNode = (n: any): n is ConstituHintNode =>
    typeof n === 'object'
    && 'hintNode' in n
    && isHintNode(n['hintNode'])
    && 'node' in n
    && Object.keys(n).length === 2
