import { SPEditor } from "@udecode/plate";

import { ConstituencyNode, DItem, NLPLabel } from "../../../../@types";
import { DepHintdata, HintData, InDepHintData, Others, TaskRet, TaskRetItem } from "../types";
import { debugLog, equalToItem, findByIdx, flatDataTableToCell, getHintNode, largerThanItem, lessThanItem } from "../utils";

import { ConstituHintNode, isConstituHintNode } from "./cHintNode";
import TaskNode, { ComparisonTask, FilterTask, ITaskNode, TASK_TYPE } from "./TaskNode";
import OpNode, { IOpNode } from "./OpNode";
import { filterOp_numOp_attrNode, filterTask_distributeOp_rangeOp2_attrNode, filterTask_hintNode, filterTask_rangeOp_distributeOp_attrNode, retrieveTask_comparisonTask } from "./rules";
import { cloneDeep } from "lodash";

export function stringSearch(search: string, datasets: Record<string, DItem[]>, datasetKey?: string): HintData[] {

  const datasetKeys = datasetKey ? [datasetKey] : Object.keys(datasets)

  // ['lastWeekByRegion', 'lastWeekByRegion2', 'lastWeekByCountry']

  return datasetKeys
    .flatMap((datasetId) => {
      const dataTable = datasets[datasetId]

      // 1. attributes
      let attrCandidates: InDepHintData[] = Object.keys(dataTable[0].data)
        .map((attr, idx) => ({
          rowId: '_null',
          attr,
          colIdx: idx,
          rowIdx: -1,
          value: attr,
          dataType: 'attr' as const,
          nodeType: 'indep' as const,
          query: search,
          datasetId,
          deps: null,
        }))
        .filter(({ attr }) => attr.split(/_| /).some(s => s.toLowerCase().startsWith(search.toLowerCase())))

      if (['byRegionDaily', 'byRegionWeek', 'lastWeekByCountry', 'lastWeekByRegion2', 'lastWeekByRegion'].includes(datasetId) && 
      (search.startsWith('ne') || search.startsWith('to'))) {
        attrCandidates = []
      }

      // 2. values
      const valueCandidates: InDepHintData[] = flatDataTableToCell(dataTable)
        .map(cell => ({
          ...cell,
          dataType: 'value' as const,
          nodeType: 'indep' as const,
          query: search,
          datasetId,
          deps: null
        }))
        .filter(({ value }) => value.split(/_| /).some(s => s.toLowerCase().startsWith(search.toLowerCase()))
          // value.toLowerCase().startsWith(search.toLowerCase())
        )
        // should group the same values together
        .reduce((o, d) => {
          if (!o.find(h => h.attr === d.attr && h.value === d.value)) {
            o.push(d)
          }
          return o
        }, [] as InDepHintData[])
      return [...attrCandidates, ...valueCandidates]
    })
}

export function buildTaskTree(node: ConstituencyNode, lv: number = 0,
  tokens: NLPLabel[], editor: SPEditor): ITaskNode | ConstituHintNode | IOpNode |
  Array<ITaskNode | ConstituHintNode | IOpNode> {
  console.group(`buildTaskTree lv_${lv}`)

  // add dataType if it is a token
  const token = tokens.find(t => t.token_idx === node?.token_idx)
  // console.log('%c token', 'background: #222; color: #000080', token)
  const hintNode = token ? getHintNode(token, editor) : undefined

  /**
   * if a leaf operator node
   * */
  console.log('node.token_idx', node.token_idx, 'hintNode', hintNode)
  if (typeof node.token_idx === 'number' && !hintNode) {
    const n = findByIdx(node.token_idx, tokens)
    console.log('node token', n)
    let ret: any
    // find extreme
    if (OpNode.match.ExtremeMaxOp(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.ExtremeMaxOp()
    } else if (OpNode.match.ExtremeMinOp(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.ExtremeMinOp()
    }
    // find comparison filter
    else if (OpNode.match.filterLessOp(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.FilterLessOp()
    } else if (OpNode.match.filterMoreOp(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.FilterMoreOp()
    } else if (OpNode.match.filterAllOp(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.FilterAllOp()
    } else if (OpNode.match.filterLastOp(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.FilterLastOp('byRegionWeek')
    }
    // compare
    else if (OpNode.match.CompareOp(n?.lemma.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.CompareDiffOp()
    }
    // find computed value
    else if (OpNode.match.ComputeSum(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.ComputeSumOp()
    } else if (OpNode.match.ComputeAvg(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.ComputeAvgOp()
    }
    // find abs
    else if (OpNode.match.Abs(n?.lemma.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.AbsOp()
    }
    // find range 
    else if (OpNode.match.rangeStart(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.RangeStartOp()
    } else if (OpNode.match.rangeEnd(n?.text.toLowerCase(), n?.tag, n?.head_text).length !== 0) {
      ret = OpNode.build.RangeEndOp()
    }
    // distribute description
    else if (OpNode.match.distributeDecrese(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.DistributeDecreseOp()
    } else if (OpNode.match.distributeIncrease(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.DistributeIncreaseOp()
    }
    // addup
    else if (OpNode.match.addupOp(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.AddupNode(n?.lemma!, 'byRegionWeek')
    }
    // find const num
    else if (OpNode.match.ConstNum(n?.text.toLowerCase(), n?.tag).length !== 0) {
      ret = OpNode.build.ConstNum(n!.text)
    }
    if (ret) {
      console.groupEnd()
      return ret
    }
  }

  /**
   * if a leaf hintNode, return TaskNode or HintNode
   * ignore placeholder here
   */
  if (hintNode && hintNode.nodeType !== 'placeholder') {
    console.groupEnd()
    return node.labels.includes('NP') && hintNode.dataType === 'value'
      ? {
        node, lv, hintNode,
        task: TASK_TYPE.filter,
        op: equalToItem,
      } as FilterTask
      : { hintNode, node }
  } else if (hintNode && hintNode.nodeType === 'placeholder' && hintNode.phType === 'RATIO') {
    // return a compareOP
    console.groupEnd()
    return OpNode.build.CompareRatioOp()
  }

  // if a middle node, check its children
  const childrenNodes = node.children.flatMap(n => buildTaskTree(n, lv + 1, tokens, editor))
  debugLog('middleNode', lv, node, 'childrenHintNodeOrNP', childrenNodes)

  // focus on NP node
  let ret: any
  // different combination leads to different resutls
  if ((node.labels.includes('NP') || node.labels.includes('VP') || node.labels.includes('S') || node.labels.includes('PP') || node.labels.includes('FRAG') || node.labels.includes('SINV')) && childrenNodes.length !== 0) {
    const cHintNodes = childrenNodes.filter(isConstituHintNode)
    const taskNodes = childrenNodes.filter(TaskNode.is.taskNode)
    const opNodes = childrenNodes.filter(OpNode.is.Op)

    switch (childrenNodes.length) {
      case 1:
        if (cHintNodes.length === 1) {
          const cHintNode = cHintNodes[0]
          if (cHintNode.hintNode.dataType === 'value') {
            ret = { node, lv, hintNode: cHintNode.hintNode, task: TASK_TYPE.filter, op: equalToItem }
          } else if (cHintNode.hintNode.dataType === 'attr') {
            ret = cHintNode
          }
        } else if (opNodes.length === 1) {
          ret = opNodes[0] as IOpNode
        } else if (taskNodes.length === 1) {
          if (node.children.some(c => c.text === 'DIFF_ATTR') && taskNodes.some(TaskNode.is.Filter)) {
            // dirty hack
            const filterTask = taskNodes.find(TaskNode.is.Filter)!
            ret = [{ hintNode: filterTask.hintNode, node: filterTask.node }, OpNode.build.AbsOp()]
          } else {
            ret = { ...taskNodes[0], node, lv }
          }
        }
        break
      case 2:
        if (cHintNodes.length === 1) {
          const cHintNode = cHintNodes[0]

          const extremeOp = opNodes.find(OpNode.is.ExtremeOp)
          const computedOp = opNodes.find(OpNode.is.ComputedOp)
          const filterOp = opNodes.find(OpNode.is.FilterOp)

          const filterTask = taskNodes.find(TaskNode.is.Filter)
          const extremeTask = taskNodes.find(TaskNode.is.Extreme)

          if (extremeOp) {
            // 1 extremeOp + hintNode
            ret = { node, lv, task: TASK_TYPE.find_extremum, hintNode: cHintNode.hintNode, op: extremeOp.op, name: extremeOp.name, }
          } else if (computedOp) {
            // 1 computeOp + hintNode
            ret = { node, lv, task: TASK_TYPE.derived_value, hintNode: cHintNode.hintNode, op: computedOp.op }
          } else if (filterOp) {
            ret = { node, lv, task: TASK_TYPE.filter, hintNode: cHintNode.hintNode, op: filterOp.op }
          }
          else if (filterTask) {
            ret = filterTask_hintNode(filterTask, cHintNode, node, lv)
          } else if (extremeTask) {
            ret = { node, lv, task: TASK_TYPE.retrieve_value, hintNode: cHintNode.hintNode, subTask: taskNodes }
          }
        } else if (cHintNodes.length === 2) {
          if (cHintNodes.some(h => h.hintNode.dataType === 'value')
            && cHintNodes.some(h => h.hintNode.dataType === 'attr')) {
            // if 1 value hintNode + 1 attr hintNode
            const attrCHintNode = cHintNodes.find(h => h.hintNode.dataType === 'attr')!
            const valueCHintNode = cHintNodes.find(h => h.hintNode.dataType === 'value')!
            // item attr
            if (attrCHintNode.hintNode.colIdx === 0) {
              ret = valueCHintNode
            } else {
              // should construct 2 subtasks
              ret = {
                node, lv, task: TASK_TYPE.retrieve_value, hintNode: attrCHintNode.hintNode,
                subTask: [{
                  node: valueCHintNode.node, lv: lv + 1, hintNode: valueCHintNode.hintNode, task: TASK_TYPE.filter, op: equalToItem
                }]
              }
            }
          }
        } else if (opNodes.length === 1) {
          if (opNodes.some(OpNode.is.FilterOp) && taskNodes.some(TaskNode.is.Retrieve)) {
            const filterOp = opNodes.find(OpNode.is.FilterOp)!
            const retrieveTask = childrenNodes.find(TaskNode.is.Retrieve)!
            // dirty hack
            const op = filterOp.op.name === 'lessThanItem' ? largerThanItem : lessThanItem
            ret = {
              node, lv, task: TASK_TYPE.filter, op, subTask: [retrieveTask]
            } as FilterTask
          } else if (opNodes.some(OpNode.is.ComputedOp) && taskNodes.some(TaskNode.is.Retrieve)) {
            const computedOp = opNodes.find(OpNode.is.ComputedOp)!
            const retrieveTask = childrenNodes.find(TaskNode.is.Retrieve)!
            ret = {
              node, lv, task: TASK_TYPE.derived_value, op: computedOp.op, hintNode: retrieveTask.hintNode, subTask: [retrieveTask]
            }
          } else if (opNodes.some(OpNode.is.CompareOp) && taskNodes.some(TaskNode.is.Filter)) {
            const compareOp = opNodes.find(OpNode.is.CompareOp)
            const filterTask = childrenNodes.find(TaskNode.is.Filter)
            ret = {
              node, lv, task: TASK_TYPE.comparison, op: compareOp?.op, notDoneYet: true, subTask: [filterTask]
            } as ComparisonTask
          } else if (opNodes.some(OpNode.is.AddupOp) && taskNodes.some(TaskNode.is.Computed)) {
            const computedTask = childrenNodes.find(TaskNode.is.Computed)!
            ret = computedTask
          }
        } else if (opNodes.some(OpNode.is.FilterOp) && opNodes.some(OpNode.is.AddupOp)) {
          // eg. last week, dirty hack
          const filterOp = opNodes.find(OpNode.is.FilterOp)
          const addupOp = opNodes.find(OpNode.is.AddupOp)!
          // should construct to ta filte task
          ret = {
            node, lv, task: TASK_TYPE.filter,
            op: filterOp?.op,
            oData: 'byRegionWeek',
            hintNode: {
              attr: 'rowIdx',
              dataType: 'value',
              value: '1'
            },
          } as FilterTask
        } else if (taskNodes.length === 2) {
          if (taskNodes.some(TaskNode.is.Filter) && taskNodes.some(TaskNode.is.Computed)) {
            const filterTask = taskNodes.find(TaskNode.is.Filter)!
            const computedTask = taskNodes.find(TaskNode.is.Computed)!
            // should merge into a computed task
            ret = {
              node, lv, task: TASK_TYPE.derived_value, hintNode: computedTask?.hintNode!, op: computedTask.op,
              subTask: [filterTask]
            }
          } else if (taskNodes.some(TaskNode.is.Filter) && taskNodes.some(TaskNode.is.Extreme)) {
            const filterTask = taskNodes.find(TaskNode.is.Filter)!
            const extremeTask = taskNodes.find(TaskNode.is.Extreme)!
            ret = {
              node, lv, task: TASK_TYPE.find_extremum, hintNode: extremeTask.hintNode, op: extremeTask.op, name: extremeTask.name,
              subTask: [filterTask]
            }
          } else if (taskNodes.every(TaskNode.is.Filter)) {
            // two filter task in sequence
            const sortedFilterTasks = taskNodes.sort((a, b) => a.lv - b.lv)
            ret = {
              ...sortedFilterTasks[0],
              node, lv,
              subTask: [sortedFilterTasks[1]]
            }
          } else if (taskNodes.some(TaskNode.is.Retrieve) && taskNodes.some(TaskNode.is.Comparison)) {
            // dirty hack
            const retrieveValueTask = taskNodes.find(TaskNode.is.Retrieve)!
            const comparisonTask = taskNodes.find(TaskNode.is.Comparison)!
            ret = retrieveTask_comparisonTask(retrieveValueTask, comparisonTask, node, lv)
          }
        }
        break
      case 3:
        if (opNodes.length === 1 && taskNodes.some(TaskNode.is.Filter) && cHintNodes.length === 1) {
          const cHintNode = cHintNodes[0]
          const filterTask = taskNodes.find(TaskNode.is.Filter)!

          if (opNodes.some(OpNode.is.ExtremeOp)) {
            // if extremeNode + 1 taskNode + 1hintNode, return a find_extreme task
            const extremeNode = opNodes.find(OpNode.is.ExtremeOp)!
            ret = {
              node, lv,
              task: TASK_TYPE.find_extremum, hintNode: cHintNode.hintNode,
              op: extremeNode.op, name: extremeNode.name,
              subTask: taskNodes
            }
          } else if (opNodes.some(OpNode.is.ComputedOp)) {
            ret = {
              node, lv,
              task: TASK_TYPE.derived_value, hintNode: cHintNode.hintNode,
              op: opNodes[0].op,
              subTask: taskNodes
            }
          } else if (opNodes.some(OpNode.is.AddupOp)) {
            ret = filterTask_hintNode(filterTask, cHintNode, node, lv)
          }
        } else if (opNodes.some(OpNode.is.FilterOp) && opNodes.some(OpNode.is.ConstNumOp) && cHintNodes.some(h => h.hintNode.dataType === 'attr')) {
          // should construct a filterTask
          const filterOp = opNodes.find(OpNode.is.FilterOp)!
          const numOp = opNodes.find(OpNode.is.ConstNumOp)!
          const attrNode = cHintNodes.find(h => h.hintNode.dataType === 'attr')!
          ret = filterOp_numOp_attrNode(filterOp, numOp, attrNode, node, lv)
        } else if (opNodes.some(OpNode.is.FilterOp) && opNodes.some(OpNode.is.ConstNumOp) && taskNodes.some(TaskNode.is.Filter)) {
          // 1 compOp + 1 numOp + 1 filter task
          // should construct a filterTask and return two filterTask
          const filterOp = opNodes.find(OpNode.is.FilterOp)!
          const filterTaskNode = taskNodes.find(TaskNode.is.Filter)!
          const numOp = opNodes.find(OpNode.is.ConstNumOp)!
          ret = {
            node, lv, task: TASK_TYPE.filter, op: filterOp.op,
            hintNode: {
              // just assume that the subtask has a hintNode, which can be a bug, since the subtask can return items, and value
              ...filterTaskNode.hintNode,
              dataType: 'value', value: numOp?.op().toString()
            },
            subTask: [filterTaskNode]
          }
        } else if (opNodes.some(OpNode.is.RangeOp) && opNodes.some(OpNode.is.DistributeOp) && taskNodes.some(TaskNode.is.Filter)) {
          // dirty hack
          // Europe reports a decline in weekly cases from TTOKEN to TTOKEN.
          const filterTaskNode = taskNodes.find(TaskNode.is.Filter)!
          const rangeOp = opNodes.find(OpNode.is.RangeOp)
          const distributeOp = opNodes.find(OpNode.is.DistributeOp)
          console.log('super dirty hack', filterTaskNode)
          ret = {
            node, lv, task: TASK_TYPE.range, op: rangeOp?.op,
            subTask: [{
              node, lv, task: TASK_TYPE.filter,
              // should add a filter here
              oData: 'byRegionWeek',
              // super dirty hack
              hintNode: { ...filterTaskNode.hintNode, attr: filterTaskNode.hintNode?.value, colIdx: -1 },
              op: distributeOp?.op,
            }]
          }
        } else if (cHintNodes.length === 2 && taskNodes.some(TaskNode.is.Filter)) {
          const attrCHintNode = cHintNodes.find(h => h.hintNode.dataType === 'attr' && h.hintNode.colIdx !== 0)!
          ret = {
            node, lv, task: TASK_TYPE.retrieve_value,
            hintNode: attrCHintNode,
            subTasks: taskNodes
          }
        } else if (taskNodes.some(TaskNode.is.Retrieve) && taskNodes.some(TaskNode.is.Comparison) && opNodes.some(OpNode.is.AbsOp)) {
          const retrieveValueTask = taskNodes.find(TaskNode.is.Retrieve)!
          const comparisonTask = taskNodes.find(TaskNode.is.Comparison)!
          ret = retrieveTask_comparisonTask(retrieveValueTask, comparisonTask, node, lv)
          const absOp = opNodes.find(OpNode.is.AbsOp)
          if (ret) {
            const _op = ret.op
            ret.op = (...args) => absOp?.op(_op(...args))
          }
        } else if (taskNodes.some(TaskNode.is.Filter) && taskNodes.some(TaskNode.is.Comparison) && cHintNodes[0].hintNode.dataType === 'attr') {
          const filterTaskNode = taskNodes.find(TaskNode.is.Filter)!
          const comparisonTask = taskNodes.find(TaskNode.is.Comparison)!
          const attrNode = cHintNodes[0].hintNode
          ret = {
            node, lv, task: TASK_TYPE.comparison,
            op: comparisonTask.op,
            subTask: [filterTask_hintNode(filterTaskNode, cHintNodes[0], node, lv), ...(comparisonTask.subTask || [])]
          }

        } else if (opNodes.some(OpNode.is.AddupOp) && opNodes.some(OpNode.is.RangeOp) && opNodes.some(OpNode.is.DistributeOp)) {
          // dirty hack
          const addupOp = opNodes.find(OpNode.is.AddupOp)
          const rangeOp = opNodes.find(OpNode.is.RangeOp)
          const distributeOp = opNodes.find(OpNode.is.DistributeOp)
          ret = [rangeOp, distributeOp]
        } else if (cHintNodes.length === 1 && opNodes.some(OpNode.is.ComputedOp) && opNodes.some(OpNode.is.AddupOp)) {
          const cHintNode = cHintNodes[0]
          const computedOp = opNodes.find(OpNode.is.ComputedOp)!
          ret = { node, lv, task: TASK_TYPE.derived_value, hintNode: cHintNode.hintNode, op: computedOp.op }
        } else if (taskNodes.some(TaskNode.is.Retrieve) && taskNodes.some(TaskNode.is.Filter) && opNodes.some(OpNode.is.CompareOp)) {
          const retrieveTask = taskNodes.find(TaskNode.is.Retrieve)
          const filterTask = taskNodes.find(TaskNode.is.Filter)
          const compareOp = opNodes.find(OpNode.is.CompareOp)
          console.log('a bit wried')
          // ret = {
          //   node, lv, task: TASK_TYPE.comparison,
          //   op: compareOp.op,
          //   subTask: [retrieveTask, ...(comparisonTask.subTask || [])]
          // }
        } else if (opNodes.some(OpNode.is.RangeOp) && opNodes.some(OpNode.is.DistributeOp) && cHintNodes.some(h => h.hintNode.dataType === 'attr')) {
          console.log('========!!!!!!!!!!!!=====')
          const rangeOp = opNodes.find(OpNode.is.RangeOp)!
          const distributeOp = opNodes.find(OpNode.is.DistributeOp)!
          const attrNode = cHintNodes[0]
          // dirty hack
          ret = {
            node, lv, task: TASK_TYPE.range, op: rangeOp?.op,
            subTask: [{
              node, lv, task: TASK_TYPE.filter,
              oData: attrNode.hintNode.datasetId,
              // should add a filter here
              hintNode: { ...attrNode.hintNode, dataType: 'value', value: '_all' },
              op: distributeOp?.op,
            } as FilterTask]
          }
        }
        break
      case 4:
        if (taskNodes.some(TaskNode.is.Filter) && taskNodes.some(TaskNode.is.Comparison) && opNodes.some(OpNode.is.AbsOp) && cHintNodes.length === 1) {
          const cHintNode = cHintNodes[0]
          const filterTask = taskNodes.find(TaskNode.is.Filter)!
          const retrieveValueTask = filterTask_hintNode(filterTask, cHintNode, node, lv)
          const comparisonTask = taskNodes.find(TaskNode.is.Comparison)!
          ret = retrieveTask_comparisonTask(retrieveValueTask, comparisonTask, node, lv)
          const absOp = opNodes.find(OpNode.is.AbsOp)
          if (ret) {
            const _op = ret.op
            ret.op = (...args) => absOp?.op(_op(...args))
          }
        } else if (opNodes.some(OpNode.is.RangeOp) && opNodes.some(OpNode.is.DistributeOp) && taskNodes.some(TaskNode.is.Filter) && cHintNodes.some(h => h.hintNode.dataType === 'attr')) {
          const filterTaskNode = taskNodes.find(TaskNode.is.Filter)!
          const rangeOp = opNodes.find(OpNode.is.RangeOp)!
          const distributeOp = opNodes.find(OpNode.is.DistributeOp)!
          const attrNode = cHintNodes[0]
          ret = filterTask_rangeOp_distributeOp_attrNode(filterTaskNode, rangeOp, distributeOp, attrNode, node, lv)
        }
        break
      case 5:
        if (opNodes.filter(OpNode.is.FilterOp).length === 2 && opNodes.filter(OpNode.is.ConstNumOp).length === 2 && cHintNodes.some(h => h.hintNode.dataType === 'attr')) {
          // should construct 2 filterTask
          const filterOps = opNodes.filter(OpNode.is.FilterOp)!
          const numOps = opNodes.filter(OpNode.is.ConstNumOp)!
          const attrNode = cHintNodes.find(h => h.hintNode.dataType === 'attr')!
          const task = (idx: number) => filterOp_numOp_attrNode(filterOps[idx], numOps[idx], attrNode, node, lv)

          ret = [task(0), task(1)] as FilterTask[]
        } else if (opNodes.some(OpNode.is.RangeOp) && opNodes.some(OpNode.is.DistributeOp) && taskNodes.some(TaskNode.is.Filter) && opNodes.some(OpNode.is.AddupOp) && cHintNodes.some(h => h.hintNode.dataType === 'attr')) {
          const filterTaskNode = taskNodes.find(TaskNode.is.Filter)!
          const distributeOp = opNodes.find(OpNode.is.DistributeOp)!
          const addupOp = opNodes.find(OpNode.is.AddupOp)
          const rangeOp = opNodes.find(OpNode.is.RangeOp)!
          const attrNode = cHintNodes[0]!
          ret = filterTask_rangeOp_distributeOp_attrNode(filterTaskNode, rangeOp, distributeOp, attrNode, node, lv)
          ret.subTask[0].oData = 'byRegionWeek'
        } else if (opNodes.length === 3 && taskNodes.length === 1 && cHintNodes.length === 1) {
          const distributeOp = opNodes.find(OpNode.is.DistributeOp)!
          const filterTaskNode2 = taskNodes.find(TaskNode.is.Filter)!
          const [rangeOp1, rangeOp2] = opNodes.filter(OpNode.is.RangeOp)
          const attrNode = cHintNodes[0].hintNode
          // super dirty hack for `The money increases from XX to XX`
          if (attrNode.attr === 'money') {
            ret = {
              node, lv, task: TASK_TYPE.range, op: rangeOp2?.op,
              subTask: [{
                node, lv, task: TASK_TYPE.filter,
                oData: attrNode.datasetId,
                // should add a filter here
                hintNode: { ...attrNode.hintNode, dataType: 'value', value: '_all' },
                op: (items: DItem[], value: any, attr: string) => {
                  console.debug('DistributeIncreaseOpEnd', items, value, attr)
                  // filter the rows that decline in `attr`
                  const rowIdx = filterTaskNode2.hintNode?.rowIdx || Infinity
                  return items.filter((item, itemIdx) => itemIdx > rowIdx)
                }
              } as FilterTask]
            }
          } else if (attrNode.datasetId === 'reProductionData') {
            ret = {
              node, lv, task: TASK_TYPE.range, op: rangeOp2?.op,
              subTask: [{
                node, lv, task: TASK_TYPE.filter,
                oData: attrNode.datasetId,
                // should add a filter here
                hintNode: { ...attrNode.hintNode, dataType: 'value', value: '_all' },
                op: (items: DItem[], value: any, attr: string) => {
                  console.debug('DistributeIncreaseOpEnd', items, value, attr)
                  // filter the rows that decline in `attr`
                  const rowIdx = filterTaskNode2.hintNode?.rowIdx || Infinity

                  return items.filter((item, itemIdx) => itemIdx < rowIdx)
                }
              } as FilterTask]
            }
          } else {
            ret = filterTask_distributeOp_rangeOp2_attrNode(distributeOp, filterTaskNode2, rangeOp2)
          }
        }
        break
      case 6:
        if (opNodes.length === 4 && taskNodes.length === 1 && cHintNodes.length === 1) {
          const addupOp = opNodes.find(OpNode.is.AddupOp)
          const distributeOp = opNodes.find(OpNode.is.DistributeOp)!
          const filterTaskNode2 = taskNodes.find(TaskNode.is.Filter)!
          const [rangeOp1, rangeOp2] = opNodes.filter(OpNode.is.RangeOp)
          const attrNode = cHintNodes[0].hintNode
          ret = filterTask_distributeOp_rangeOp2_attrNode(distributeOp, filterTaskNode2, rangeOp2)
        }
        break
      default:
        ret = undefined //{ node, lv, task: TASK_TYPE.retrieve_value, subTask: childrenNodes as ITaskNode[] }
    }
  }

  if (ret) {
    debugLog('ret', ret)
    console.groupEnd()
    return ret
  }

  console.groupEnd()
  return childrenNodes
}


/**
 * can return items, values
 */
export function evaluateTaskNode(node: ITaskNode, items: DItem[], rawData: Record<string, DItem[]>,
  tokens: NLPLabel[], constituency: ConstituencyNode): TaskRet | undefined {
  // evaluate the children first
  const subResults = node.subTask ? node.subTask.map(t => evaluateTaskNode(t, items, rawData, tokens, constituency)) : []

  console.group('evaluateTaskNode')
  console.log('subReults', subResults, node)

  switch (node.task) {
    case TASK_TYPE.filter: {
      const { op, hintNode, oData } = node
      console.log('node', node)

      let targetItems: DItem[] = oData ? rawData[oData] : items
      let value: string | number | null = null
      let attr: string | null = null

      // check subResult first, if has subResult, use subResult
      if (subResults.length === 1 && subResults[0]?.type === 'value') {
        const valueHintNode = subResults[0].data[0] // use the 1st one as default
        value = valueHintNode.value
        attr = valueHintNode.attr
      } else if (subResults.length === 1 && subResults[0]?.type === 'item') {
        targetItems = subResults[0].data
      }

      if (hintNode && hintNode.dataType !== 'value') {
        console.warn('The hintNode in filter task is not a value node', hintNode)
      }

      // the hintNode has higher priority than subResults, if has hintNode, use hintNode
      if (hintNode?.dataType === 'value') {
        value = hintNode.value
        attr = hintNode.attr
      }

      console.log('Target items before filter', targetItems)
      if (value !== null && attr !== null) {
        if (oData === 'trainingData' || oData === 'trainingData2') {
          targetItems = targetItems.sort((a, b) => a.rowIdx - b.rowIdx)
        }
        targetItems = op(targetItems, value, attr)
      } else {
        console.warn('value or attr is null', 'hintNode', hintNode)
      }

      console.log('filter task, targetItems', targetItems)
      console.groupEnd()
      return { type: 'item', data: targetItems }
    }
    case TASK_TYPE.retrieve_value: {
      if (subResults.length === 1 && subResults[0]?.type === 'value') {
        console.groupEnd()
        return subResults[0]
      }

      const { hintNode } = node
      const targetItems = (subResults.find(r => r?.type === 'item') as TaskRetItem)?.data || items
      const attr = hintNode!
      console.log('hintNode', hintNode, 'node', node)

      console.groupEnd()
      return attr.colIdx === 0
        ? { type: 'item', data: targetItems }
        : {
          type: 'value', data: targetItems.map(item => {
            const value = item.data[attr?.attr!].toString()
            return {
              rowId: item.id,
              value,
              dataType: 'value',
              nodeType: 'dep',
              attr: attr?.attr,
              colIdx: attr?.colIdx, // cells[0].colIdx,
              rowIdx: item.rowIdx,
              id: item.id,
              query: '',
              deps: { tokens, constituency }, //tokens
            }
          })
        }
    }
    case TASK_TYPE.find_extremum: {
      const { hintNode, op, name } = node
      // input
      const attr = hintNode //|| (subResults.find(r => r?.type === 'attr') as TaskRetAttr)?.data
      const targetItems = (subResults.find(r => r?.type === 'item') as TaskRetItem)?.data || items
      console.log('find_extremum targetItems', targetItems)
      // output
      const item = op!(targetItems, attr?.attr!)

      const others = {
        type: name,
        items: [...targetItems].sort((a, b) => name === 'max'
          ? b.data[attr?.attr!] - a.data[attr?.attr!]
          : a.data[attr?.attr!] - b.data[attr?.attr!]
        ),
        attr: attr?.attr!
      }

      console.groupEnd()
      return { type: 'item', data: [item], others }
    }
    case TASK_TYPE.derived_value: {
      const { hintNode, op } = node
      // input
      let targetItems: DItem[] = items
      let attr: string | null = hintNode.attr

      if (subResults.length) {
        const subResult = subResults[0]
        if (subResult?.type === 'item') {
          targetItems = subResult.data
        } else if (subResult?.type === 'value') {
          targetItems = subResult.data.map(d =>
          ({
            id: d.rowId, rowIdx: d.rowIdx, virtualAttr: [], data: {
              _item: d.rowId,
              [d.attr]: d.value
            },
          }))
          attr = subResult.data[0].attr
        }
      }

      // output
      const value = op(targetItems.map(d => d.data[attr!]))

      // if sum, should add a new row
      let others: any | undefined
      // hack
      if (op.name === 'sum') {
        console.log('sum task ====>', targetItems)
        others = { type: 'sum', items: targetItems }
      }

      console.log('derive value task', value)
      console.groupEnd()
      return {
        type: 'value', data: [{
          rowId: "_null",
          value: value.toString(),
          nodeType: 'dep',
          dataType: 'computed',
          attr: op.name === 'sum' ? hintNode.attr : `${op.name}(${hintNode?.attr})`,
          colIdx: hintNode?.colIdx!,
          rowIdx: op.name === 'sum' ? items.length : -1,
          deps: { tokens, constituency }
        }],
        others
      }
    }
    case TASK_TYPE.comparison: {
      const { op, hintNode } = node
      console.log('Evaluate Comparison Task')
      console.log(subResults)
      let rowId = '_null'
      let value1: number | string = 0
      let value2: number | string = 0
      let attr = 'unknown'
      let colIdx = -1
      let rowIdx = -1

      let others: Others | undefined
      if (subResults.length === 2) {
        if (subResults.some(s => s?.type === 'value') && subResults.some(s => s?.type === 'item')) {
          const valueSRes = subResults.find(s => s?.type === 'value')!.data[0] as DepHintdata
          let itemSRes = subResults.find(s => s?.type === 'item')!.data as DItem[]
          console.log('itemSRes', itemSRes)
          // dirty hack, data transfer
          if ('date' in itemSRes[0].data) {
            itemSRes = Object.keys(itemSRes[0].data)
              .filter(k => k !== 'date')
              .map((region, rowIdx) => {
                return {
                  id: region,
                  virtualAttr: [],
                  rowIdx, data: {
                    region,
                    ...itemSRes[0].data[region]
                  }
                } as DItem
              })
            const theItem = itemSRes.find(d => d.id === valueSRes.rowId)
            value2 = theItem?.data[valueSRes.attr] || 0 // hack, 0 as the default

          } else if (itemSRes.length === 6 || itemSRes.length === 7) {
            value2 = itemSRes.reduce((o, d) => {
              o = o + +d.data[valueSRes.attr]
              return o
            }, 0)
            console.log('value2', value2)
            itemSRes = itemSRes.map(d => {
              const newD = cloneDeep(d)
              newD.data[valueSRes.attr] = value2
              return newD
            })
          } else if ('state' in itemSRes[0].data) {
            value2 = itemSRes[0].data[valueSRes.attr]
          }


          attr = valueSRes.attr
          colIdx = valueSRes.colIdx
          rowIdx = valueSRes.rowIdx
          rowId = valueSRes.rowId
          value1 = valueSRes.value
          // value2 = theItem?.data[valueSRes.attr] || 0 // hack, 0 as the default

          others = { type: 'compare', items: itemSRes, attr }
        }
      }

      const diff = op(value1, value2)
      console.log('Comparison diff', diff, 'value1', value1, 'value2', value2)

      console.groupEnd()
      return {
        type: 'value', others, data: [{
          rowId,
          value: `${((diff / +value2) * 100).toFixed(1)}%`,
          valueFormat: (value1, value2) => `${((op(value1, value2) / +value2) * 100).toFixed(1)}%`,
          nodeType: 'dep',
          dataType: 'computed',
          attr: `diff_${attr}`,
          colIdx: colIdx + 1, rowIdx,
          deps: { tokens, constituency }
        }, {
          rowId,
          value: diff.toString(),
          valueFormat: op,
          nodeType: 'dep',
          dataType: 'computed',
          attr: `diff_${attr}`,
          colIdx: colIdx + 1, rowIdx,
          deps: { tokens, constituency }
        }, {
          rowId,
          value: (diff / +value2).toFixed(2),
          valueFormat: (value1, value2) => `${((op(value1, value2) / +value2)).toFixed(2)}`,
          nodeType: 'dep',
          dataType: 'computed',
          attr: `diff_${attr}`,
          colIdx: colIdx + 1, rowIdx,
          deps: { tokens, constituency }
        }]
      }
    }
    case TASK_TYPE.range: {
      // dirty hack
      const { op } = node
      console.log('Range task')
      const targetItems = subResults.find(d => d?.type === 'item') as TaskRetItem
      const data = targetItems?.data.map(item => {
        const keys = Object.keys(item.data)
        return {
          rowId: item.id,
          value: item.data[keys[0]],
          attr: keys[0],
          dataType: 'value',
          nodeType: 'dep',
          colIdx: 0,
          rowIdx: item.rowIdx,
          query: '',
          deps: { tokens, constituency }
        }
      }) as DepHintdata[]

      console.groupEnd()
      return {
        type: 'value', data
      }
    }
    case TASK_TYPE.add_up: {
      console.warn('!! Add up task should be deprecated !!')
      return
      // const { op, oData } = node
      // let targetItems: DItem[] = oData ? rawData[oData] : items
      // const data = op(targetItems)
      // console.log('return add_up', data)
      // console.groupEnd()
      // return {
      //   type: 'item', data
      // }
    }
  }

  console.groupEnd()
}

export function newTaskEngine(tokens: NLPLabel[], constituency: ConstituencyNode | null,
  dataTable: DItem[], rawData: Record<string, DItem[]>, editor: SPEditor) {

  let candidates: Array<DepHintdata> = []
  console.group('NewTaskEngine')
  if (!tokens) {
    console.warn('No tokens')
    console.groupEnd()
    return { candidates }
  }
  if (!constituency) {
    debugLog('No constituency')
    console.groupEnd()
    return { candidates }
  }

  debugLog('Constituency', constituency)

  const taskTree = buildTaskTree(constituency, 0, tokens, editor)
  debugLog('taskTree', taskTree)
  // step2, evaluate the tasks based on NP
  const taskResult = evaluateTaskNode(taskTree as ITaskNode, dataTable, rawData, tokens, constituency)
  debugLog('result', taskResult)

  if (taskResult?.type === 'value') {
    candidates = candidates.concat(taskResult.data)
  } else if (taskResult?.type === 'item') {
    candidates = candidates.concat(flatDataTableToCell(taskResult.data)
      .filter(c => c.colIdx === 0)
      .map(c => ({
        ...c,
        dataType: 'value' as const,
        nodeType: 'dep' as const,
        query: '',
        deps: { tokens, constituency }
      })))
  }

  let others = taskResult?.others

  console.groupEnd()
  return { candidates, taskTree, others }
}

export type { ITaskNode } from './TaskNode'