import './style.scss'

import React, { forwardRef, useEffect, useImperativeHandle, useMemo } from 'react';
import {
  createBoldPlugin,
  createHistoryPlugin,
  createItalicPlugin,
  createNodeIdPlugin,
  createParagraphPlugin,
  createReactPlugin,
  createPlateComponents,
  createPlateOptions,
  createUnderlinePlugin,
  ELEMENT_PARAGRAPH,
  HeadingToolbar,
  PlatePlugin,
  Plate,
  SPEditor,
  withProps,
  useStoreEditorValue,
  usePlateActions,
  MARK_ITALIC,
  StyledLeaf,
  useStoreEditorRef,
  useEventEditorId,
} from '@udecode/plate';
import { ReactEditor } from 'slate-react';
import { Editor, Transforms } from 'slate';

import { useCompare } from '../../App';
import { HintElement } from '../HintElement'
import { HintSelect } from '../HintSelect'
import { OpElement } from '../OpElement';

import { useAutoHintPlugin, ELEMENT_HINT, HintNode, HintPluginOptions, isHintNode, debugLog, HintData, isInDepHintNode, setHintNode, isDepHintNode, getHintNode, reactiveUpdate } from './autoHint'
import {
  editableProps,
} from './pluginOptions';
import { ToolbarButtonsBasicMarks } from './Toolbars';
import { withStyledPlaceHolders } from './withStyledPlaceHolders';
import { createOPPlugin, ELEMENT_OP } from './operator'
import { DItem } from '../../@types';

const defaultOptions = createPlateOptions()

const initialValuePlainText = [
  {
    type: ELEMENT_PARAGRAPH,
    id: "qwerqwer",
    children: [
      {
        text: '',
      },
    ],
  },
]

interface Props extends HintPluginOptions {
  options?: Record<string, any>
  onClickIO?: (e: React.MouseEvent<HTMLSpanElement, MouseEvent>, hintNode: HintNode) => void
  onDbClickIO?: (e: React.MouseEvent<HTMLSpanElement, MouseEvent>, hintNode: any) => void
  // @deprecated
  onChange?: (data: { hints: HintNode[], id: string }) => void
}

export type WordProcessorRef = {
  updateHintNode: (payload: Array<[HintData, string]>) => void
  updateBarChartHintNode: (payload: Array<[HintData, DItem]>) => void
  updateLinechartHintNode: (payload: Array<[HintData, Date]>) => void
  clearContent: () => void
}

export const DatatableContext = React.createContext<{
  // dataTable: DItem[],
  rawData: Record<string, DItem[]>
}>({
  // dataTable: [], 
  rawData: {}
});
export const WordProcessor = React.memo(forwardRef<WordProcessorRef, Props>((props, ref) => {

  const {
    options = defaultOptions,
    onClickIO,
    onDbClickIO,
    // onChange = (e) => { },
    onTab,
    onSelectHint,
    onFocusCurSentence,
    onChangeDataPhrases,
    onOthers
  } = props
  // const dataTable = useCompare(props.dataTable ?? [])
  const rawData = useCompare(props.rawData ?? [])

  const { getHintSelectProps, plugin: hintPlugin } = useAutoHintPlugin({
    // dataTable: dataTable.filter(d => !d.exclude),
    rawData,
    onTab, onSelectHint, onFocusCurSentence, onChangeDataPhrases, onOthers
  });
  const pluginsMemo = useMemo(() => {
    const plugins: PlatePlugin<ReactEditor & SPEditor>[] = [
      // editor
      createReactPlugin(),
      createHistoryPlugin(),
      // element
      createParagraphPlugin(),
      createOPPlugin(),

      // mark
      createBoldPlugin(),
      createItalicPlugin(),
      createUnderlinePlugin(),

      // plugings
      createNodeIdPlugin(),
      hintPlugin
    ];

    return plugins;
  }, [hintPlugin]);

  const components = useMemo(() => {

    return withStyledPlaceHolders(createPlateComponents({
      [ELEMENT_HINT]: withProps(HintElement, {
        onSelectOption: (data, hintNode, path) => {
          console.log('data:', data, 'hintNode:', hintNode, 'path', path)
          const { returnNode, others } = data
          if (returnNode && others) {
            // onOthers?.(others, returnNode?.valueFormat)
          }
        },
        onMouseDown: (e, hintNode, path) => {
        },
        onClick: (e, hintNode, path) => {
          onClickIO?.(e, hintNode)
        },
        onDoubleClick: (e, hintNode, path) => {
          onDbClickIO?.(e, hintNode)
        }
      }),
      [ELEMENT_OP]: withProps(OpElement, {}),
      // [MARK_BOLD]: withProps(StyledLeaf, {
      //   styles: {
      //     root: {
      //       color: 'green'
      //     }
      //   }
      // }),
      [MARK_ITALIC]: withProps(StyledLeaf, {
        styles: {
          root: {
            color: 'red'
          }
        }
      })
    }))
  }, [onClickIO, onDbClickIO])

  /**
   * update node values once the dataTable is updated 
   * */
  const value = useStoreEditorValue()
  const editorActions = usePlateActions()
  const editor = useStoreEditorRef(useEventEditorId('focus'));

  useEffect(() => {
    console.group('Update rawData', value, editor)

    if (editor) {
      // update indep node
      console.group('Update Indep node')
      for (const [node, nodePath] of Editor.nodes(editor, { at: [0], match: isInDepHintNode, voids: true })) {
        if (node.dataType === 'value' && node.rowIdx !== -1) {
          const row = rawData[node.datasetId].find(row => row.id === node.rowId)
          const newData = row?.data[node.attr]
          debugLog('New Data', newData, 'rowIdx', node.rowIdx, 'attr', node.attr)
          setHintNode(editor, { ...node, value: newData.toString() }, nodePath)
        }
      }
      console.groupEnd()

      // update dep node
      console.group('Update Dep node')
      for (const [node, nodePath] of Editor.nodes(editor, { at: [0], match: isDepHintNode, voids: true })) {
        // re-evaluate
        console.log('hintNode re-evaluate', node)
        if (!node.deps) {
          console.warn('Dep node has no deps', node)
          continue
        }
        const datasetKey = node.deps.tokens.filter(t => t.hintPath)
          .map(t => getHintNode(t, editor as any))
          .find(h => h?.datasetId)?.datasetId
        const dataTable = rawData[datasetKey]

        const { returnNode, others } = reactiveUpdate(node.deps.tokens, node.deps.constituency, node as any, nodePath, dataTable, rawData, editor as any)
        console.log('data-driven update returnNode and others', returnNode, others)
        others && onOthers?.(others, returnNode?.valueFormat)
      }
      console.groupEnd()

    }
    // const newValue = value?.map(n => {
    //   if (n.type === ELEMENT_PARAGRAPH) {
    //     n = {
    //       ...n,
    //       children: n.children.map(child => {
    //         if (isHintNode(child) && child.dataType === 'value' && child.rowIdx !== -1) {
    //           console.log(child)
    //           const row = rawData[child.datasetId].find(row => row.id === child.rowId)
    //           const newData = row?.data[child.attr!]
    //           debugLog('New Data', newData, 'rowIdx', child.rowIdx, 'attr', child.attr)

    //           return {
    //             ...child,
    //             children: [{ text: newData.toString() }],
    //             value: newData.toString()
    //           }
    //         }
    //         return child
    //       })
    //     }
    //   }
    //   return n
    // })

    // if (newValue) {
    //   editorActions.setValue(newValue)
    //   // editor && Transforms.move(editor)
    // }

    console.groupEnd()
  }, [rawData])

  useImperativeHandle(ref, () => ({
    updateHintNode: (payLoad) => {
      console.log(payLoad)
      console.log(value)
      const newValue = value?.map(node => {
        if (node.type === ELEMENT_PARAGRAPH) {
          node = {
            ...node,
            children: node.children.map(child => {
              if (isHintNode(child) && payLoad.some(p => p[0]['id'] === child['id'])) {
                const target = payLoad.find(p => p[0]['id'] === child['id'])!
                return {
                  ...child,
                  value: target[1],
                  children: [{ text: target[1] }],
                }
              }
              return child
            })
          }
        }
        return node
      })

      if (newValue) {
        setImmediate(() => {
          editorActions.setValue(newValue)
          editor && Transforms.select(editor, [0, newValue[0].children.length - 1])
        })
      }
    },
    updateBarChartHintNode: (payLoad) => {
      const newValue = value?.map(node => {
        if (node.type === ELEMENT_PARAGRAPH) {
          node = {
            ...node,
            children: node.children.map(child => {
              if (isHintNode(child) && payLoad.some(p => p[0]['id'] === child['id'])) {
                const target = payLoad.find(p => p[0]['id'] === child['id'])!
                const newItem = target[1]
                return {
                  ...child,
                  rowId: newItem.id,
                  rowIdx: newItem.rowIdx,
                  value: newItem.data[child.attr],
                  children: [{ text: newItem.data[child.attr] }],
                }
              }
              return child
            })
          }
        }
        return node
      })

      if (newValue) {
        setImmediate(() => {
          editorActions.setValue(newValue)
          editor && Transforms.select(editor, [0, newValue[0].children.length - 1])
        })
      }
    },
    updateLinechartHintNode: (payLoad) => {
      const newValue = value?.map(node => {
        if (node.type === ELEMENT_PARAGRAPH) {
          node = {
            ...node,
            children: node.children.map(child => {
              if (isHintNode(child) && payLoad.some(p => p[0]['id'] === child['id'])) {
                const target = payLoad.find(p => p[0]['id'] === child['id'])!
                const v = target[1].toISOString().slice(0, 10).replaceAll('-', '/')
                return {
                  ...child,
                  // rowId: 
                  value: v,
                  children: [{ text: v }],
                }
              }
              return child
            })
          }
        }
        return node
      })

      if (newValue) {
        setImmediate(() => {
          editorActions.setValue(newValue)
          editor && Transforms.select(editor, [0, newValue[0].children.length - 1])
        })
      }
    },
    clearContent: () => {
      editorActions.setValue(initialValuePlainText)
    }
  }))

  return (
    <div className={`word-processor`}>
      <DatatableContext.Provider value={{ rawData }}>
        <Plate
          id={'Examples/Playground'}
          plugins={pluginsMemo}
          components={components}
          options={options}
          editableProps={editableProps}
          initialValue={initialValuePlainText}
        >
          <HeadingToolbar>
            <ToolbarButtonsBasicMarks />
          </HeadingToolbar>

          <HintSelect {...getHintSelectProps()} />
        </Plate>

      </DatatableContext.Provider>
    </div>
  );
}))
WordProcessor.displayName = 'WordProcessor'
WordProcessor.whyDidYouRender = true

export type { ITaskNode, TaskRet, Others, HintData } from './autoHint'
export type { OpNode } from './operator'
export { isHintNode, headOf } from './autoHint'