import React, { Component, createRef, RefObject, MouseEvent } from 'react'
import clsx from 'clsx'

import './style.scss'

export interface Option {
  label?: string;
  value: string;
  idx: number
  className?: string;
}
export interface Group {
  type: "group";
  name: string;
  items: Option[];
}

const isGroup = (obj: any): obj is Group => typeof obj === 'object' && obj.type === 'group'
const isOption = (obj: any): obj is Option => typeof obj === 'object' && typeof obj.value === 'string'

const baseClassName = 'Dropdown'

export interface Props {
  value: Option | string;
  options: (Group | Option | string)[];
  // className?: string;
  disabled?: boolean;
  // arrowClosed?: React.ReactNode,
  // arrowOpen?: React.ReactNode,
  onChange?: (arg: Option) => void;
  onFocus?: (arg: boolean) => void;
  // placeholder?: String;
}

interface State {
  selected: Option | string
  isOpen: boolean
}

export class Dropdown extends Component<Props, State> {
  dropdownRef: RefObject<HTMLDivElement> = createRef()

  constructor(props: Props) {
    super(props)
    this.state = {
      selected: this.parseValue(props.value, props.options),
      isOpen: true
    }
    console.log(this.state)
  }

  componentDidUpdate(prevProps: Props) {
    if (this.props.value !== prevProps.value) {
      const selected = this.parseValue(this.props.value, this.props.options)
      if (selected !== this.state.selected) {
        this.setState({ selected })
      }
    }
  }

  onMouseDown = (event: MouseEvent<HTMLDivElement, globalThis.MouseEvent>) => {
    this.props.onFocus?.(this.state.isOpen)
    if (event.type === 'mousedown' && event.button !== 0) return

    event.stopPropagation()
    event.preventDefault()

    if (!this.props.disabled) {
      this.setState({
        isOpen: !this.state.isOpen
      })
    }
  }

  parseValue(value: string | Option, options: (Group | Option | string)[]) {
    let selectedOption: Option | undefined

    // if value is string but options are objects
    if (typeof value === 'string') {
      for (let i = 0, num = options.length; i < num; i++) {
        const option = options[i]
        if (isGroup(option)) {
          selectedOption = option.items.find(item => item.value === value)
        } else if (isOption(option) && option.value === value) {
          selectedOption = option
        }
      }
    }

    return selectedOption || value
  }

  setValue = (value: string, label: string, idx: number) => {
    console.log('setValue')
    let newState = {
      selected: {
        value,
        label,
        idx
      },
      isOpen: false
    }
    if (newState.selected !== this.state.selected && this.props.onChange) {
      this.props.onChange(newState.selected)
    }
    this.setState(newState)
  }

  renderOption(option: Option | string, idx: number) {

    const value = isOption(option) ? option.value : option
    const label = isOption(option) ? (option.label || option.value) : option
    const isSelected = isOption(this.state.selected)
      ? value === this.state.selected.value
      : value === this.state.selected

    const optionClass = clsx(isOption(option) ? option.className : '', {
      [`${baseClassName}-option`]: true,
      'is-selected': isSelected
    })

    return (
      <div
        key={value}
        className={optionClass}
        // onMouseDown={() => this.setValue(value, label)}
        onClick={(e) => {
          e.stopPropagation()
          this.setValue(value, label, isOption(option)? option.idx : idx)
        }}
        role='option'
        aria-selected={isSelected ? 'true' : 'false'}>
        {label}
      </div>
    )
  }

  renderMenu() {
    const { options } = this.props
    let ops = options.map((option, idx) => {
      if (isGroup(option)) {
        return (
          <div className={`${baseClassName}-group`} key={option.name} role='listbox' tabIndex={-1}>
            <div className={`${baseClassName}-title`}>
              {option.name}
            </div>
            {option.items.map((item) => this.renderOption(item, idx))}
          </div>
        )
      } else {
        return this.renderOption(option, idx)
      }
    })

    return ops.length ? ops : <div className={`${baseClassName}-noresults`}>
      No options found
    </div>
  }

  render() {

    // const disabledClass = this.props.disabled ? 'Dropdown-disabled' : ''
    const valueLabel = typeof this.state.selected === 'string'
      ? this.state.selected
      : this.state.selected.label

    const placeholderClass = clsx({
      [`${baseClassName}-placeholder`]: true,
      'is-selected': typeof this.state.selected === 'string' || this.state.selected.value !== ''
    })

    const menu = this.state.isOpen
      ? <div className={`${baseClassName}-menu`} aria-expanded='true'>
        {this.renderMenu()}
      </div>
      : null

    return (
      <div ref={this.dropdownRef} className={clsx('Dropdown-root', this.state.isOpen && 'is-open')}>
        <div className={'Dropdown-control'}
          onMouseDown={this.onMouseDown}
          aria-haspopup='listbox'>
          {/* <div className={placeholderClass}>
            {valueLabel}
          </div> */}
          {/* <div className={`${baseClassName}-arrow-wrapper`}>
            {
              // arrowOpen && arrowClosed
              //   ? this.state.isOpen ? arrowOpen : arrowClosed
              //   : 
            }
            <span className={`${baseClassName}-arrow`} />
          </div> */}
        </div>
        {menu}
      </div>
    )
  }
}