import { ConstituencyNode, DItem } from "../../../../@types";
import { ConstituHintNode } from "./cHintNode";
import { ConstNumNode, DistributeNode, FilterNode, RangeNode, RangeOp } from "./OpNode";
import { ComparisonTask, FilterTask, RetrieveTask, TASK_TYPE } from "./TaskNode";

export function filterTask_hintNode(filterTask: FilterTask, cHintNode: ConstituHintNode, node: ConstituencyNode, lv: number) {

    let ret: any
    if (cHintNode.hintNode.colIdx !== 0) {
        if (cHintNode.hintNode.dataType === 'attr') {
            // if not name attribute ---> a retrieve_value task
            ret = { node, lv, task: TASK_TYPE.retrieve_value, hintNode: cHintNode.hintNode, subTask: [filterTask] }
        } else if (cHintNode.hintNode.dataType === 'value') {
            ret = { node, lv, task: TASK_TYPE.filter, hintNode: cHintNode.hintNode, subTask: [filterTask] }
        }

    } else {
        // otherwise, equal to a filter task
        ret = { ...filterTask, node, lv }
    }

    return ret
}

export function retrieveTask_comparisonTask(retrieveValueTask: RetrieveTask, comparisonTask: ComparisonTask, node: ConstituencyNode, lv: number) {
    if (comparisonTask?.notDoneYet) {
        return {
            node, lv, task: TASK_TYPE.comparison,
            op: comparisonTask.op,
            subTask: [retrieveValueTask, ...(comparisonTask.subTask || [])]
        } as ComparisonTask
    }
}

export function filterOp_numOp_attrNode(filterOp: FilterNode, numOp: ConstNumNode, attrNode: ConstituHintNode, node: ConstituencyNode, lv: number) {

    return {
        node, lv, task: TASK_TYPE.filter, op: filterOp?.op,
        hintNode: {
            ...attrNode.hintNode,
            dataType: 'value', value: numOp?.op().toString()
        }
    } as FilterTask
}

export function filterTask_rangeOp_distributeOp_attrNode(filterTaskNode: FilterTask, rangeOp: RangeNode, distributeOp: DistributeNode, attrNode: ConstituHintNode, node: ConstituencyNode, lv: number) {

    return {
        node, lv, task: TASK_TYPE.range, op: rangeOp?.op,
        subTask: [{
            node, lv, task: TASK_TYPE.filter,
            oData: 'byRegionDaily',
            // dirty hack for table transfer
            hintNode: { ...filterTaskNode.hintNode, attr: attrNode.hintNode.value, colIdx: -1 },
            op: distributeOp?.op,
        } as FilterTask]
    }
}

export function filterTask_distributeOp_rangeOp2_attrNode(distributeOp: DistributeNode, filterTaskNode2: FilterTask, rangeOp2: RangeNode ) {
    distributeOp['op'] = (items: DItem[], value: any, attr: string) => {
        console.debug('DistributeDecreseOpEnd', items, value, attr)
        // filter the rows that decline in `attr`
        const rowIdx = filterTaskNode2.hintNode?.rowIdx || Infinity
        return items.filter((item, itemIdx) => itemIdx < rowIdx)
    }
    return [rangeOp2, distributeOp]
}