import { BaseEditor, Editor, Path, Transforms } from "slate"
import { SPEditor } from "@udecode/plate"

import { ConstituencyNode, DItem, NLPLabel } from "../../../@types"
import { newTaskEngine } from "./taskEngine"
import { DepHintdata, PlaceholderHintData } from "./types"

export function setHintNode(editor: BaseEditor, props: any, path: Path) {
  Transforms.setNodes(editor, props, { at: path })
  Transforms.insertText(editor, props.value, { at: [...path, 0], voids: true })
}

export function flatWrongOp(editor: BaseEditor, path: Path) {
  // should highlight the data op if exist
  const opNodeEntry = Editor.previous(editor, { at: path, match: node => node['underline'] })
  if (opNodeEntry) {
    const [opNode, opNodePath] = opNodeEntry
    // flag it as italic
    Transforms.setNodes(editor, {
      italic: true
    } as any, { at: opNodePath })
  }
}

export function unFlatWrongOp(editor: BaseEditor, path: Path) {
  // should highlight the data op if exist
  const opNodeEntry = Editor.previous(editor, { at: path, match: node => node['underline'] })
  if (opNodeEntry) {
    const [opNode, opNodePath] = opNodeEntry
    Transforms.unsetNodes(editor, 'italic', { at: opNodePath })
  }
}


export function reactiveUpdate(tokens: NLPLabel[], constituency: ConstituencyNode | null,
  oNode: PlaceholderHintData | DepHintdata, oNodePath: Path,
  dataTable: DItem[], originalData: Record<string, DItem[]>,
  editor: SPEditor,
  // candidates?: DepHintdata[]
) {
  console.group('ReactiveUpdate')
  // if (!candidates) {
  const { others, candidates } = newTaskEngine(tokens, constituency, dataTable, originalData, editor)
  // candidates = result.candidates
  // }

  if (!candidates.length) {
    console.groupEnd()
    return { returnNode: undefined, others }
  }

  // should get values based on the placeholder type
  let nNode: PlaceholderHintData | DepHintdata | undefined

  if (!constituency) {
    console.groupEnd()
    return { returnNode: nNode, others }
  }

  if (oNode.nodeType === 'placeholder') {
    switch (oNode.phType) {
      case 'VALUE':
        nNode = {
          ...oNode,
          attr: candidates[0].attr,
          colIdx: candidates[0].colIdx,
          value: candidates[0].value.toString(),
          rowIdx: candidates[0].rowIdx,
          deps: candidates[0].deps
        }
        break
      case 'COUNT_ITEMS':
        nNode = {
          ...oNode,
          attr: candidates[0].attr,
          colIdx: candidates[0].colIdx,
          value: candidates.length.toString(),
          deps: candidates[0].deps
        }
        break
      case 'DIFF_ATTR':
        nNode = {
          ...oNode,
          valueFormat: candidates[0].valueFormat,
          attr: candidates[0].attr,
          colIdx: candidates[0].colIdx,
          value: candidates[0].value.toString(),
          deps: candidates[0].deps
        }
        break
      case 'RATIO':
        nNode = {
          ...oNode,
          valueFormat: candidates[0].valueFormat,
          rowId: candidates[0].rowId,
          rowIdx: candidates[0].rowIdx,
          attr: candidates[0].attr,
          colIdx: candidates[0].colIdx,
          value: candidates[0].value.toString(),
          deps: candidates[0].deps
        }
        break
    }
  } else if (oNode.nodeType === 'dep') {
    nNode = {
      ...oNode,
      rowId: candidates[oNode.selectedIdx!].rowId,
      rowIdx: candidates[oNode.selectedIdx!].rowIdx,
      attr: candidates[oNode.selectedIdx!].attr,
      colIdx: candidates[oNode.selectedIdx!].colIdx,
      value: candidates[oNode.selectedIdx!].value.toString(),
      deps: candidates[oNode.selectedIdx!].deps
    }
  }

  console.debug('reactive update candidates', candidates, 'newNode', nNode, 'oldNode', oNode)
  if (!nNode) {
    // should highlight the data op if exist
    flatWrongOp(editor, oNodePath)
  } else {
    // update the node
    setHintNode(editor, nNode, oNodePath)
    // unflat the wrong op if exist
    unFlatWrongOp(editor, oNodePath)
  }

  Transforms.select(editor, Editor.end(editor, [0]))
  console.groupEnd()
  return { returnNode: nNode, others }
}